.sect_works {
	margin-bottom: 70px;

	@include min768 {
		margin-bottom: 20px;
	}

	.title-36 {
		margin-bottom: 50px;

		@include min768 {
			margin-bottom: 20px;
		}
	}

	&__items {
		display: grid; 
		grid-auto-columns: 1fr; 
		grid-template-columns: 1fr 1fr 1fr 1fr; 
		grid-template-rows: 1fr 1fr 1fr; 
		gap: 30px 30px; 
		grid-template-areas: 
			"item1 item2 item3 item4"
			"item5 item5 item6 item8"
			"item5 item5 item7 item8"; 
		width: 100%; 
		height: 100%;

		@include min768 {
			grid-auto-columns: 1fr; 
			grid-template-columns: 1fr 1fr; 
			grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr; 
			gap: 20px 20px; 
			grid-template-areas: 
				"item1 item2"
				"item3 item3"
				"item3 item3"
				"item6 item5"
				"item8 item8"
				"item8 item8"
				"item8 item8"
				"item8 item8"
				"item7 item4"; 

		}

		.item1 {
			grid-area: item1;

			
		}
		.item2 { grid-area: item2; }
		.item3 {
			grid-area: item3; 
			
		}
		.item4 { grid-area: item4; }
		.item5 { grid-area: item5; }
		.item6 { grid-area: item6; }
		.item7 { grid-area: item7; }
		.item8 { grid-area: item8; }

		.item {
			border-radius: 30px;
			overflow: hidden;

			@include min768 {
				border-radius: 10px;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 30px;

				@include min768 {
					border-radius: 10px;
				}
			}
		}

		
	}
}