.header {
	position: relative;

	@include min1200 {
		padding-top: rem(100);
	}

	@include min1050 {
		.container-max {
			margin-left: 0;
			margin-right: 0;
		}
	}

	@include min768 {
		padding-top: rem(84);
		overflow: hidden;
	}

	&__top {
		// position: relative;
		padding: rem(35) 0;

		@include min1600 {
			padding: rem(25) 0;
		}

		@include min1200 {
			position: fixed;
			top: 0;
			width: 100%;
			z-index: 51;
			background-color: white;
		}

		@include min768 {
			padding: rem(17) 0;
		}

		>.container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
		}

		&_left {
			display: flex;
			flex-wrap: wrap;
			align-items: center;


			.button_header_smart {
				display: none;

				@include min1200 {
					display: flex;
				}
			}

			#button_header_burger {
				svg.icon-close {
					display: none;
				}
			}

			#button_header_burger.active {

				svg.icon-burger {
					display: none;
				}

				svg.icon-close {
					display: block;
				}
			}

			ul {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				@include min1200 {
					display: none;
				}

				li {
					margin-right: rem(38);

					@include min1600 {
						margin-right: rem(19);
					}

					&:first-child {
						margin-right: rem(45);

						@include min1600 {
							margin-right: rem(22.5);
						}

						a {
							display: flex;
							align-items: center;

							svg {
								margin-right: rem(20);
							}
						}
					}

					&:last-child {
						margin-right: 0;
					}

					a {
						color: var(--c-bgray);

						@include min1600 {
							font-size: rem(14);
						}

						&:hover {
							color: var(--c-green-global);
						}
					}
				}
			}

			.social__links {
				margin-left: rem(65);

				@include min1600 {
					margin-left: rem(32);
				}

				@include min1340 {
					display: none;
				}
			}
		}

		&_center {
			position: absolute;
			top: 0;
			left: 50%;
			margin-left: rem(-128.5);
			z-index: 10;

			@include min1600 {
				margin-left: rem(-110);
			}

			@include min1340 {
				margin-left: rem(-134);
			}

			@include min1200 {
				margin-left: rem(-100);
			}

			@include min768 {
				margin-left: rem(-57);
			}

			.logo {
				width: rem(257);
				height: rem(180);
				display: block;

				@include min1600 {
					width: rem(200);
					height: auto;
				}

				@include min768 {
					width: rem(114);
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		&_right {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.button_header_smart {
				display: none;

				@include min1200 {
					display: flex;

					
				}
			}

			#button_header_phone {
				svg.icon-close {
					display: none;
				}
			}

			#button_header_phone.active {

				svg.icon-phone {
					display: none;
				}

				svg.icon-close {
					display: block;
				}
			}

			.block_text_right {
				margin-right: rem(30);
				margin-top: rem(3);

				@include min1600 {
					margin-right: rem(16);
				}

				@include min1200 {
					display: none;
				}

				@include min1340 {
					.image {
						display: none;
					}
				}

				&:last-of-type {
					margin-right: 0;
				}
			}

			.button_green {
				margin-left: rem(24);

				@include min1600 {
					margin-left: rem(12);
				}

				@include min1200 {
					display: none;
				}
			}
		}
	}

	&__bottom {
		position: relative;
		// background: linear-gradient(90deg, #197641 0%, #1E9652 100%);
		background: url(../img/header_overlay.jpg) no-repeat;
		background-size: cover;
		border-radius: rem(30);
		// overflow: hidden;

		display: flex;
		flex-wrap: wrap;

		margin-bottom: rem(100);

		@include min1050 {
			border-radius: 0;
		}

		@include min540 {
			padding-bottom: rem(84);
		}

		&_text {
			padding-top: rem(128);
			padding-bottom: rem(128);

			margin-left: rem(80);

			flex: 0 0 45%;
			max-width: 45%;

			display: flex;
			flex-direction: column;

			@include min1600 {
				padding-top: rem(90);
				padding-bottom: rem(90);
				margin-left: rem(40);
			}

			@include min1340 {
				flex: 0 0 55%;
				max-width: 55%;
				padding-bottom: rem(25);
			}

			@include min1050 {
				flex: 0 0 70%;
				max-width: 70%;
				margin-left: rem(20);
				padding-top: rem(45);
			}

			@include min540 {
				flex: 0 0 calc(100% - 40px);
				max-width: calc(100% - 40px);
				// margin-right: rem(20);
				padding-top: rem(20);
			}

			> span {
				color: #E0E0E0;
				font-size: rem(24);
				line-height: rem(30);
				margin-bottom: rem(20);

				@include min1600 {
					font-size: rem(18);
					margin-bottom: rem(10);
				}

				@include min1340 {
					font-size: rem(16);
				}

				@include min1050 {
					font-size: rem(18);
				}

				@include min768 {
					font-size: rem(16);
				}

				@include min540 {
					font-size: rem(14);
				}
			}

			h1 {
				font-size: rem(64);
				font-weight: 800;
				color: white;
				margin-bottom: rem(20);

				@include min1600 {
					font-size: rem(52);
					margin-bottom: rem(10);
				}

				@include min1340 {
					font-size: rem(42);
				}

				@include min1050 {
					font-size: rem(52);
				}

				@include min {
					font-size: rem(46);
				}

				@include min768 {
					font-size: rem(32);
				}

				@include min540 {
					font-size: rem(22);
				}

				span {
					color: #F2C94C;
				}
			}

			p {
				font-size: rem(18);
				line-height: rem(30);
				color: #E0E0E0;
				max-width: rem(455);

				@include min1600 {
					max-width: rem(350);
					font-size: rem(14);
				}

				@include min1050 {
					font-size: rem(18);
				}

				@include min768 {
					font-size: rem(16);
					max-width: rem(220);
				}

				@include min540 {
					font-size: rem(14);
					line-height: 140%;
					max-width: rem(190);
				}
			}
		}

		&_image {
			flex: 0 0 40%;
			max-width: 40%;

			margin-top: rem(20);
			margin-left: rem(40);

			@include min1340 {
				position: absolute;
				top: rem(80);
				right: rem(40);

				flex: 0 0 45%;
				max-width: 45%;
			}

			@include min1050 {
				flex: 0 0 55%;
				max-width: 55%;

				top: rem(140);
				right: rem(-50);

				margin-left: rem(20);
			}

			@include min {
				flex: 0 0 70%;
				max-width: 70%;
				top: rem(175);
				right: rem(-100);
			}

			@include min540 {
				flex: 0 0 rem(350);
				max-width: rem(350);
				top: rem(100);
				right: rem(-80);
			}

			@include min350 {
				top: rem(140);
				flex: 0 0 rem(308);
				max-width: rem(308);
			}

			// @include min768 {
			// 	flex: 0 0 90%;
			// 	max-width: 90%;
			// }
		}

		&_links {
			// margin-left: rem(80);
			margin-top: rem(60);
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			// @include min1600 {
			// 	margin-left: rem(40);
			// }

			@include min1050 {
				margin-top: rem(180);
				flex-direction: column-reverse;
				align-items: flex-start;

				.button_play {
					margin-bottom: rem(20);

					span {
						max-width: rem(120);
						text-align: left;
					}
				}
			}

			@include min540 {
				margin-top: rem(200);
			}

			.button_yellow {
				margin-right: rem(30);

				@include min1600 {
					margin-right: rem(14);
				}

				@include min540 {
					width: 100%;
					justify-content: center;
				}
			}
		}

		&_flag {
			position: absolute;
			top: rem(90);
			right: 0;

			width: rem(86);

			display: flex;
			flex-direction: column;
			align-items: center;

			background: url(../img/svg/flag_bg.svg) no-repeat;
			background-size: cover;
			box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);

			padding: rem(15) 0 rem(11) 0;

			@include min540 {
				left: 0;
				top: rem(240);
				right: auto;
				width: rem(60);

				background: url(../img/svg/flag_bg_smart.svg) no-repeat;
				background-size: cover;
			}

			b {
				font-size: rem(36);
				color: white;
				margin-bottom: rem(20);

				@include min540 {
					font-size: rem(24);
					margin-bottom: rem(4);
				}
			}

			span {
				font-size: rem(12);
				line-height: rem(16);
				color: white;
				text-align: center;
				margin-bottom: rem(12);

				@include min540 {
					font-size: rem(8);
					line-height: rem(10);
					margin-bottom: rem(2);
				}
			}

			img {
				max-width: rem(95);
				width: rem(95);
				align-self: flex-end;
				// display: block;

				@include min540 {
					max-width: rem(64);
					width: rem(64);
				}
			}

		}

		&_slider {
			position: absolute;
			width: rem(900);

			// margin-left: auto;
			right: rem(75);
			bottom: rem(-56);

			@include min1600 {
				right: rem(40);
				width: rem(740);
			}

			@include min1340 {
				position: relative;
				flex: 0 0 rem(900);
				max-width: rem(900);
				right: auto;

				margin-left: auto;
				margin-right: auto;
			}

			@include min {
				flex: 0 0 100%;
				max-width: 100%;
				margin-left: rem(20);
				margin-right: auto;
			}

			@include min540 {
				position: absolute;
				bottom: rem(-126);
				width: 100%;
			}

			.swiper-container {
				margin-right: rem(-30);
				padding-right: rem(30);

				margin-bottom: rem(-50);
				padding-bottom: rem(50);

				.swiper-wrapper {
					margin-right: rem(-30);
					padding-right: rem(30);
				}

				.swiper-slide {
					box-shadow: 0px 30px 30px rgba(0, 0, 0, 0.05);
				}

				.swiper-nav {
					position: absolute;
					top: rem(16);
					z-index: 10;
					right: rem(65);

					display: none;

					@include min768 {
						display: block;
					}

					&-right {
						width: rem(50);
						height: rem(50);
						border-radius: 50%;
						background-color: white;
						box-shadow: 0px 20px 40px rgba(12, 87, 60, 0.05);

						display: flex;
						align-items: center;
						justify-content: center;
					}
				}
			}

			.item {

				display: flex;
				flex-direction: column;
				padding: rem(20);

				background: url(../img/svg/header_item.svg) no-repeat;
				background-size: 100%;

				height: rem(215);

				

				&__image {
					// margin-bottom: rem(24);
					height: rem(70);
				}

				b {
					color: #4D4A4B;
					line-height: 140%;
					margin-bottom: rem(20);

					@include min1600 {
						font-size: rem(14);
						margin-bottom: rem(10);
					}
				}

				p {
					font-size: rem(15);
					color: #4D4A4B;
					line-height: 140%;

					@include min1600 {
						font-size: rem(13);
					}
				}
			}
		}
	}

}

.header__mini {
	margin-bottom: rem(60);

	@include min768 {
		margin-bottom: rem(28);
	}

	.header__top {
		height: rem(120);
		background-color: white;
		padding: 2.1875rem 0;
		box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);

		@include min1340 {
			height: rem(84);
			padding: 1.0625rem 0;
		}
	}
}