:root {
  --font-default: 'Montserrat', sans-serif;
  --container-maxw: 1760px;
  --container: 1600px;
  --mobile-px: 20px;
  --c-black: #333333;
  --c-gray: #828282;
  --c-bgray: #4F4F4F;
  --c-gray-light: #BDBDBD;
  --c-green-global: #219653;
  --c-green: #197641;
  --c-white-light: #F2F2F2;
  --c-yellow: #EEC900;
  --c-yellow-light: #F2C94C;
  --transition: 250ms ease;
}

* {
  outline: none;
  border: 0;
  margin: 0;
  padding: 0;
}

html {
  font-size: 100%;
}

button {
  background-color: transparent;
}

a {
  text-decoration: none;
}

a, button {
  transition: var(--transition);
}

li {
  list-style: none;
}

body {
  font-family: var(--font-default);
  font-size: 1rem;
  min-width: 320px;
}

.relative {
  position: relative;
}

.container {
  max-width: var(--container);
  margin: 0 auto;
}

@media (max-width: 1600px) {
  .container {
