.field-set {
  position: relative;
}

.field-set::before {
  content: "";
  position: absolute;
  top: -0.375rem;
  right: 1.25rem;
  z-index: 1;
  background-color: var(--c-green-global);
  display: block;
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  border: 0.1875rem solid white;
}

.field-set.field-not::before {
  display: none;
}

