.page_blog {
	margin-bottom: rem(100);

	@include min768 {
		margin-bottom: rem(30);
	}

	> .container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		height: 100%;

		@include min1050 {
			// flex-wrap: wrap;
		}
	}

	.title-h1 {
			margin-bottom: rem(40);
			font-size: rem(32);
			font-weight: 800;
			flex: 0 0 100%;
			width: 100%;
			max-width: 100%;
			color: var(--c-black);

			@include min1050 {
				font-size: rem(24);
				margin-bottom: rem(20);
			}
		}

	&__left {
		flex: 0 0 1200px;
		max-width: 1200px;
		margin-right: rem(26);

		@include min1600 {
			flex: 0 0 70%;
			max-width: 70%;
		}

		@include min1200 {
			flex: 0 0 67%;
			max-width: 67%;
		}

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;

			order: 2;
		}

		.title-h1, .title-h2, .title-h3, .title-h4, .title-h5 {
			color: var(--c-black);
			font-family: 'Roboto', sans-serif;
		}

		

		.title-h2 {
			font-size: rem(22);
			line-height: 160%;
			margin-bottom: rem(25);

			@include min1050 {
				margin-bottom: rem(20);
			}

			@include min768 {
				line-height: rem(24);
			}
		}



		img {
			border-radius: rem(30);
			margin-bottom: rem(40);

			@include min1050 {
				margin-bottom: rem(20);
				border-radius: 0;
				min-height: rem(200);
				object-fit: cover;
			}

			@include min768 {
				margin-left: -20px;
				width: calc(100% + 40px);
				max-width: calc(100% + 40px);
			}
		}

		&-container {
			max-width: 920px;
			margin-right: auto;
			margin-left: auto;
		}

		.text-line {
			position: relative;
			font-size: rem(18);
			font-weight: bold;
			line-height: 160%;
			color: #2D2D2D;
			padding-left: rem(36);
			margin-bottom: rem(30);
			font-family: 'Roboto', sans-serif;

			@include min768 {
				font-size: rem(14);
				padding-left: rem(20);
				margin-bottom: rem(24);
			}

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				background-color: #2D2D2D;
				width: rem(2);
				height: 100%;
			}
			
		}

		.text-lite {
			font-size: rem(18);
			line-height: 160%;
			color: #2D2D2D;
			margin-bottom: rem(40);
			font-family: 'Roboto', sans-serif;

			@include min768 {
				font-size: rem(14);
				margin-bottom: rem(20);
			}
		}

		.text-block-list {
			display: flex;
			flex-direction: column;
			margin-bottom: rem(40);

			@include min768 {
				margin-bottom: rem(25);
			}

			span {
				font-size: rem(12);
				font-weight: 500;
				line-height: 160%;
				text-transform: uppercase;
				font-family: 'Roboto', sans-serif;
				margin-bottom: rem(4);
			}

			.text-green {
				color: #619B3F;
			}

			.text-red {
				color: #C60000;
			}

			p {
				margin-bottom: rem(20);

				@include min768 {
					font-size: rem(14);
					margin-bottom: rem(12);
				}
			}

			ul {
				display: flex;
				flex-direction: column;

				li {
					position: relative;
					font-size: rem(18);
					line-height: 160%;
					color: #2D2D2D;
					font-family: 'Roboto', sans-serif;
					padding-left: rem(35);

					@include min768 {
						font-size: rem(14);
						padding-left: rem(18);
					}

					&::before {
						content: "";
						position: absolute;
						top: 50%;
						transform: translateY(-6px);
						left: rem(5);
						width: rem(6);
						height: rem(6);
						border-radius: 50%;
						border: rem(1) solid #619B3F;
					}
				}
			}
		}

		.table-block {
			display: flex;
			flex-wrap: wrap;
			border: rem(1) solid #EBEBEB;
			margin-bottom: rem(30);

			@include min768 {
				flex-direction: column;
				margin-left: -20px;
				width: calc(100% + 40px);
				border: 0;
			}

			&__items {
				flex: 1;
				display: flex;
				flex-direction: column;

				@include min768 {
					flex-direction: row;
					border-bottom: rem(1) solid #EBEBEB !important;

					&:first-child {
						flex-direction: row-reverse;

						.item {
							background-color: #619B3F !important;
							padding: 0 rem(35);

							b, span {
								color: white;
							}
						}
					}

					.item {
						background-color: white !important;

						b, span {
							color: var(--c-black) !important;
						}
					}
				}

				.item {
					height: rem(50);
					display: flex;
					align-items: center;
					padding: 0 rem(15);

					@include min768 {
						flex: 0 0 50%;
						max-width: 50%;
					}

					b, span {
						white-space: nowrap;
						font-family: 'Roboto', sans-serif;
						font-size: rem(14);

						@include min768 {
							white-space: normal;
						}
					}

					&:first-child {
						background-color: #619B3F;

						b, span {
							color: white;
						}
					}

					&:last-child {
						background-color: white;
					}
				}

			}
		}

		.block-col-2 {
			display: flex;
			flex-wrap: wrap;

			&-item {
				&:first-child {
					flex: 0 0 rem(650);
					max-width: rem(650);
					margin-right: rem(30);

					@include min1050 {
						flex: 0 0 100%;
						max-width: 100%;
						margin-right: 0;
						margin-bottom: rem(20);
					}
				}

				&:last-child {
					flex: 1;


					@include min1050 {
						flex: 0 0 calc(100% + 40px);
						max-width: calc(100% + 40px);
						margin-left: -20px;

						img {
							margin-left: 0;
							width: 100%;
							min-height: rem(411);
							object-position: top;
						}
					}
				}

				ul {
					display: flex;
					flex-direction: column;

					&:first-child {
						margin-bottom: rem(60);

						@include min1050 {
							margin-bottom: rem(30);
						}

						li {
							&::before {
								content: "+";
								color: #619B3F;
								font-size: 18px;
								margin-right: rem(4);
							}
						}
					}

					&:last-child {
						li {
							&::before {
								content: "-";
								color: #C60000;
								font-size: 18px;
								margin-right: rem(4);
							}
						}
					}

					li {
						display: flex;
						align-items: center;
						font-family: 'Roboto', sans-serif;
						font-size: rem(18);
						line-height: 160%;
						color: var(--c-black);
						margin-bottom: rem(5);

						&:last-child {
							margin-bottom: 0;
						}
					}
				}
			}
		}

		.list-number {
			margin-bottom: rem(30);
			counter-reset: list-counter;

			li {
				counter-increment: list-counter;
				margin-bottom: rem(32);
				font-size: rem(18);
				line-height: 160%;
				color: var(--c-black);
				font-family: 'Roboto', sans-serif;
				display: flex;
				align-items: center;

				@include min768 {
					font-size: rem(14);
					margin-bottom: rem(20);
				}

				&::before {
					content: counter(list-counter);
					color: #619B3F;
					font-size: rem(18);
					line-height: 160%;
					border-radius: rem(5);
					border: rem(1) solid #619B3F;
					flex: 0 0 rem(32);
					width: rem(32);
					height: rem(32);
					display: flex;
					align-items: center;
					justify-content: center;
					margin-right: rem(32);

					@include min1050 {
						margin-right: rem(14);
						font-size: rem(14);
					}
				}

				&:last-child {
					margin-bottom: 0;
				}
			}
		}

		.quote {
			position: relative;
			font-size: rem(18);
			line-height: 160%;
			font-family: 'Roboto', sans-serif;
			color: var(--c-black);
			padding: rem(16) rem(76);
			background-color: #F3F8FB;
			margin-bottom: rem(40);

			@include min768 {
				padding: rem(47) rem(20) rem(20) rem(20);
				margin-bottom: rem(34);
				font-size: rem(14);
				margin-top: rem(40);
			}

			&::before {
				content: "";
				position: absolute;
				z-index: 2;
				// padding: rem(14) rem(10);
				width: rem(50);
				height: rem(50);
				// border-radius: 50%;
				// background-color: white;
				// display: flex;
				// align-items: center;
				// justify-content: center;
				left: rem(15);
				top: rem(-25);
				background: url(../img/svg/quote.svg) no-repeat;

				@include min768 {
					left: 50%;
					transform: translateX(-50%);
				}
			}
		}

		
	}

	&__right {
		flex: 1;
		align-self: baseline;
		position: sticky;
		top: 20px;
		margin-top: rem(-30);

		@include min1050 {
			margin-top: 0;
			top: 0;
			position: static;
			margin-bottom: rem(30);
		}
	}

	&__main_article {
			display: flex;
			flex-wrap: wrap;
			margin-bottom: rem(30);

			.left {
				flex: 0 0 rem(785);
				max-width: rem(785);
				margin-right: 30px;

				@include min1340 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-right: 0;
					margin-bottom: rem(30);
				}

				@include min768 {
					.item_article__text {
						&:last-of-type {
							display: none;
						}
					}
				}

				@include max1050 {
					.item_article__image {
						height: rem(482);
					}
					.item_article__title {
						font-size: rem(36);
					}
					.item_article__text {
						margin-bottom: rem(30);

						&:last-of-type {
							margin-bottom: 0;
						}
					}
				}
				
			}

			.right {
				flex: 1;
				display: flex;
				flex-direction: column;

				@include min1340 {
					flex-direction: row;
					flex-wrap: wrap;
				}
				
				.item_article {
					margin-bottom: rem(30);

					@include min1340 {
						flex: 0 0 calc((100% - 30px) / 2);
						max-width: calc((100% - 30px) / 2);
						margin-right: rem(30);

						&:nth-child(2n) {
							margin-right: 0;
						}
					}

					@include min540 {
						flex: 0 0 100%;
						max-width: 100%;
						margin-right: 0;

					}

					&:last-child {
						margin-bottom: 0;
					}
				}
			}
		}
	&__list_article {
		display: flex;
		flex-wrap: wrap;

		.item_article {
			flex: 0 0 calc((100% - 60px) / 3);
			max-width: calc((100% - 60px) / 3);
			margin-right: rem(30);
			margin-bottom: rem(30);

			@include min768 {
				flex: 0 0 calc((100% - 20px) / 2);
				max-width: calc((100% - 20px) / 2);
				margin-right: rem(20);

				&:nth-child(2n) {
					margin-right: 0;
				}
			}

			@include min540 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-right: 0;
			}

			@include max768 {
				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			
		}
	}
}

.breadcrumbs {
	// display: flex;
	margin-bottom: rem(10);
	
	display: flex;
	align-items: center;

	@include min540 {
		flex-wrap: wrap;
	}

	.container {
		display: flex;
		align-items: center;
	}

	a, span {
		font-size: rem(14);
	}

	a {
		position: relative;
		color: #7C7C7C;
		margin-right: rem(22);
		white-space: nowrap;

		&:first-of-type {
			color: var(--c-green-global);
		}

		&:hover {
			text-decoration: underline;
		}

		&::after {
			content: "";
			position: absolute;
			top: 50%;
			right: rem(-15);
			transform: translateY(-3.5px);
			background: url(../img/svg/arrow.svg) no-repeat;
			display: block;
			width: 6px;
			height: 10px;
			pointer-events: none;
		}
	}

	span {
		color: #7C7C7C;
	}
}

.aside_blog {
	padding: rem(30);
	border-radius: rem(30);
	background-color: var(--c-green-global);

	display: flex;
	flex-direction: column;

	@include min1600 {
		padding: rem(20);
		border-radius: rem(20);
	}

	@include min540 {
		padding: rem(16);
	}

	&__title {
		margin-bottom: rem(32);
		text-transform: uppercase;
		font-weight: bold;
		font-size: rem(18);
		font-family: 'Roboto', sans-serif;
		color: white;

		@include min768 {
			margin-bottom: 0;
			font-size: rem(14);
		}

		.desktop {
			@include min768 {
				display: none;
			}
		}

		.mobile {
			display: none;

			@include min768 {
				display: flex;
				align-items: center;
				justify-content: space-between;
				width: 100%;

				span {
					font-size: rem(14);
					color: white;
					text-transform: uppercase;
					font-weight: bold;
				}
			}
		}
	}

	&__list {
		display: flex;
		flex-direction: column;

		@include min768 {
			display: none;
		}

		li {
			margin-bottom: rem(20);
			padding-bottom: rem(20);
			display: flex;
			align-items: center;

			border-bottom: rem(1) solid #4AAC74;

			@include min1600 {
				margin-bottom: rem(10);
				padding-bottom: rem(10);
			}

			&:last-child {
				padding-bottom: 0;
				margin-bottom: 0;
				border-bottom: 0;
			}

			a {
				display: flex;
				align-items: center;

				svg {
					margin-right: rem(10);

					path {
						fill: #CBF0DB;
						transition: 250ms ease;
					}
				}
				span {
					font-size: rem(14);
					color: #CBF0DB;
					text-transform: uppercase;
					transition: 250ms ease;
					font-family: 'Roboto', sans-serif;
					white-space: nowrap;

					@include min1600 {
						font-size: rem(13);
					}

					// @include min540 {
					// 	font-size: rem(13);
					// }
				}

				&:hover {
					svg {
						path {
							fill: white;
						}
					}

					span {
						color: white;
					}
				}
			}

			a.active {
				svg {
					path {
						fill: white;
					}
				}

				span {
					color: white;
					font-weight: bold;
				}
			}
		}
	}

	&__list.active {
		display: flex;
		margin-top: rem(22);
	}
}

.page_nav {
	margin-top: rem(60);

	@include min540 {
		margin-top: rem(30);
	}

	display: flex;
	align-items: center;
	
	&__item {
		margin-right: rem(10);
		flex: 0 0 rem(40);
		width: rem(40);
		height: rem(40);
		border-radius: 50%;
		display: none;
		align-items: center;
		justify-content: center;
		background-color: white;
		border: rem(1) solid #E0E0DD;
		font-size: rem(14);
		transition: 250ms ease;

		@include max540 {
			display: flex;
		}

		@include min540 {
			&:first-child, &:nth-child(2) {
				display: flex;
			}
		}

		&:hover {
			background-color: var(--c-green-global);
			border-color: var(--c-green-global);
			color: white;
		}
	}

	&__item.active {
		font-weight: bold;
		color: var(--c-green-global);

		&:hover {
			color: white;
		}
	}

	&__sep {
		width: rem(40);
		height: rem(40);
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: rem(10);

		@include min540 {
			display: none;
		}
	}

	&__next {
		height: rem(40);
		display: flex;
		align-items: center;
		border: rem(1) solid #E0E0DD;
		padding: 0 rem(15);
		border-radius: rem(30);

		transition: 250ms ease;

		@include min540 {
			flex: 1;
			justify-content: space-between;
		}

		* {
			transition: 250ms ease;
		}

		&:hover {
			background-color: var(--c-green-global);
			border-color: var(--c-green-global);
			
			span {
				color: white;
			}

			svg {
				path {
					fill: white;
				}
			}
		}
		
		span {
			font-size: rem(14);
			margin-right: rem(26);
		}
	}
}

.w-blog {
	.sect_article {
		@include min768 {
			display: block;
		}
	}
}