.sect_specification {
	position: relative;
	margin-bottom: rem(150);

	@include min540 {
		margin-bottom: rem(30);
	}

	.container-max {
		border-radius: rem(30);
		background-color: #F8F8F8;
		padding: rem(80);
		overflow: hidden;
		height: rem(790);

		@include min1600 {
			height: rem(670);
		}

		@include min1340 {
			padding: rem(32);
		}

		@include min540 {
			height: rem(350);
			border-radius: 0;
			width: 100%;
			margin: 0;
			padding: rem(20);
		}
	}

	.title-36 {
		margin-bottom: rem(40);

		@include min540 {
			margin-bottom: rem(20);
		}
	}

	&_tab_nav {

		display: flex;
		flex-wrap: wrap;
		margin-bottom: rem(30);

		button {
			position: relative;
			margin-right: rem(30);
			font-weight: 900;
			font-size: rem(14);
			color: var(--c-black);

			&:last-child {
				margin-right: 0;
			}

			&::before {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: rem(2);
				background-color: var(--c-green-global);

				opacity: 0;

				transition: var(--transition);
			}
		}

		button.active {
			color: var(--c-green-global);
			// border-bottom: rem(2) solid var(--c-green-global);

			&::before {
				opacity: 1;
			}
		}

		@include max1050 {
			display: none;
		}
	}

	&_tab_wrap {
		display: flex;
		flex-wrap: wrap;
	}

	&__left {

		padding-right: rem(30);

		@include min1050 {
			padding-right: 0;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			width: rem(67);
			top: 20%;
			height: rem(567);
			box-shadow: 0px 0px 100px rgba(0, 0, 0, 0.05);

			@include min1050 {
				display: none;
			}
		}

		&::after {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			z-index: 1;
			width: rem(230);
			height: rem(700);
			background-color: #F8F8F8;

			@include min1050 {
				display: none;
			}
		}
	}

	&__right {
		padding-left: rem(30);

		@include min1050 {
			padding-left: 0;
		}
	}

	.tab_item {
		@include min1050 {
			display: none;
		}
	}

	.tab_item.tab_item__active {
		@include min1050 {
			display: block;
		}
	}

	&__left,
	&__right {
		position: relative;
		flex: 0 0 50%;
		max-width: 50%;

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
		}

		.title {
			font-size: rem(18);
			font-weight: 800;
			color: #4F4F4F;
			margin-bottom: rem(90);
			display: block;

			@include min1600 {
				margin-bottom: rem(45);
			}

			@include min1050 {
				display: none;
			}
		}
		
		.list {
			display: flex;
			flex-direction: column;

			position: relative;
			z-index: 3;

			&__item {
				display: flex;
				flex-wrap: wrap;

				margin-bottom: rem(50);

				@include min1600 {
					margin-bottom: rem(28);
				}

				@include min540 {
					margin-bottom: rem(20);
				}

				&:last-child {
					margin-bottom: 0;
				}

				.image {
					width: rem(40);
					height: rem(40);
					margin-right: rem(20);

					@include min540 {
						margin-right: rem(10);
						width: rem(30);
						height: rem(30);

						svg {
							width: 100% !important;
							height: 100% !important;
						}
					}

					svg {
						path {
							fill: #BDBDBD;
						}
					}
				}

				.text {
					display: flex;
					flex-direction: column;

					b {
						color: var(--c-bgray);
						font-size: rem(12);
						text-transform: uppercase;
						margin-bottom: rem(4);

						@include min540 {
							font-size: rem(10);
							margin-bottom: rem(3);
						}
					}

					span {
						color: var(--c-bgray);
						font-size: rem(18);
						font-weight: 300;

						@include min540 {
							font-size: rem(14);
						}
					}
				}
			}
		}

		img {
			position: absolute;
			top: 0;
			right: 65px;
			z-index: 2;
			// width: 100%;
			// height: 100%;
			// object-fit: cover;

			@include min1600 {
				width: 90%;
			}

			@include min1340 {
				// width: 85%;
				top: rem(72);
			}

			@include min1050 {
				width: 50%;
				top: 0;
			}

			@include min768 {
				width: 48%;
			}

			@include min540 {
				right: rem(-50);
				top: rem(14);
				width: rem(260);
			}
		}
	}

	> .container {
		position: relative;
	}

	

	// .block_green_overlay {
	// 	position: relative;
	// 	z-index: 10;
	// 	bottom: rem(-146);
	// }
}

.block_abs {
		width: 100%;
		position: absolute;
		bottom: rem(-100);
		z-index: 10;

		@include min1600 {
			bottom: rem(-100);
			// width: calc(100% - 40px);
		}

		@include min540 {
			position: relative;
			bottom: rem(24);
		}
	}