.title-36 {
	font-size: rem(36);
	font-weight: 800;
	color: var(--c-black);

	span {
		color: var(--c-green-global);
	}

	@include min {
		font-size: rem(18);
	}
}

.section_calc {
	margin-bottom: rem(100);

	@include min540 {
		margin-top: rem(20);
	}

	.swiper-container {

		margin: -30px;
		padding: 30px;

		@include min {
			margin: -20px;
			padding: 20px;
		}

		@include min540 {
			display: flex;
			flex-direction: column;
		}
	}

	&__header {
		margin-bottom: rem(32);

		.title-36 {
			margin-bottom: rem(3);

			@include min {
				margin-bottom: rem(10);
			}
		}

		.text {
			font-size: rem(18);
			line-height: 140%;
			color: var(--c-gray);
			max-width: rem(395);

			@include min {
				font-size: rem(12);
				max-width: 100%;
			}

			span {
				color: var(--c-green);
			}
		}

	}

	.swiper-slide {
		display: flex;
		flex-wrap: wrap;
		// overflow: hidden;
		opacity: 0 !important;

		@include min {
			height: 0;
			// height: auto;
		}

		@include min540 {
			overflow: visible;
		}
	}

	.swiper-slide-active {
		opacity: 1 !important;
		@include min {
			display: flex;
			height: 100%;
		}
	}

	&__left {
		// flex: 0 0 rem(647);
		// max-width: rem(647);

		flex: 0 0 45%;
		max-width: 45%;

		padding-right: rem(60);

		@include min1340 {
			flex: 0 0 50%;
			max-width: 50%;
		}

		@include min1050 {
			flex: 0 0 50%;
			max-width: 50%;
		}

		@include min {
			flex: 0 0 100%;
			max-width: 100%;
			padding-right: 0;
		}

		&-title {
			margin-bottom: rem(38);

			display: flex;
			align-items: center;
			flex-wrap: wrap;

			@include min {
				margin-bottom: rem(13);
			}

			span {
				font-size: rem(24);
				font-weight: 500;
				color: var(--c-black);
				margin-left: rem(20);

				@include min {
					font-size: rem(14);
					margin-left: rem(10);
				}

				
			}
		}

		&-title.mb-10 {
			margin-bottom: rem(10);
		}

		&-subtitle {
			display: flex;
			align-items: center;
			margin-left: rem(40);
			margin-bottom: rem(40);

			@include min {
				margin-left: 0;
			}

			svg {
				margin-right: rem(10);
			}

			span {
				font-size: rem(14);
				font-weight: 500;
				color: #828282;

				@include min540 {
					font-size: rem(10);
				}
			}
		}

		&-selected {
			display: flex;
			flex-direction: column;

			.selected_items {
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				margin-bottom: rem(50);

				@include min540 {
					margin-bottom: rem(15);
				}

				.item_sale {
					cursor: pointer;
					position: relative;
					width: calc((100% - 30px) / 2);
					height: rem(190);

					margin-right: rem(30);

					@include min1600 {
						// width: rem(150);
						height: rem(160);
					}

					@include min1050 {
						margin-right: 0;
						margin-bottom: rem(30);
						width: 100%;
						height: rem(120);
					}

					&:last-child {
						margin-right: 0;
					}

					&:hover {
						.text {
							// background-color: #197641;
							background: #197641 url(../img/calc/sale_item.png) no-repeat;
							background-size: 100%;

							&__icon {
								margin-bottom: rem(4);

								svg {

									&:first-child {
										display: none;
									}

									&:last-child {
										display: block;
									}

									path {
										fill: white;
									}
								}
							}

							&__title {
								color: white;
							}

							&__descr {
								color: white;
							}
						}

						.check {
							opacity: 1;
						}
					}
					

					input {
						display: none;

						&:checked {
							~ .text {
								// background-color: #197641;
								background: #197641 url(../img/calc/sale_item.png) no-repeat;
								background-size: 100%;

								.text__icon {
									margin-bottom: rem(4);

									svg {

										&:first-child {
											display: none;
										}

										&:last-child {
											display: block;
										}

										path {
											fill: white;
										}
									}
								}

								.text__title {
									color: white;
								}

								.text__descr {
									color: white;
								}
							}

							~ .check {
								opacity: 1;
							}
						}
					}

					.check {
						transition: var(--transition);
						position: absolute;
						top: rem(-11);
						right: rem(-11);
						z-index: 15;
						opacity: 0;
					}

					.text {
						width: 100%;
						height: rem(190);
						border-radius: rem(10);

						padding: rem(20);
						background-color: white;
						box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);

						display: flex;
						flex-direction: column;

						// transition: var(--transition);

						@include min1600 {
							// width: rem(150);
							height: rem(160);
						}

						@include min1050 {
							width: 100%;
							height: rem(120);
						}

						&__icon {
							margin-bottom: rem(4);
							svg {

								&:last-child {
									display: none;
								}

								path {
									fill: #CFD0CF;
								}
							}
						}

						&__title {
							font-size: rem(48);
							color: #CFD0CF;

							@include min1600 {
								font-size: rem(40);
							}

							@include min540 {
								font-size: rem(16);
							}
						}

						&__title.fz-29 {
							font-size: rem(27);

							@include min1600 {
								font-size: rem(20);
							}

							@include min540 {
								font-size: rem(16);
							}
						}

						&__descr {
							color: #CFD0CF;
							font-size: rem(14);
							font-weight: 500;

							@include min540 {
								font-size: rem(10);
							}
						}

					}

				}

				.item_color {
					cursor: pointer;
					position: relative;
					flex: 0 0 rem(80);
					// width: rem(80);
					height: rem(80);
					border-radius: rem(10);

					width: calc((100% - 275px) / 5);

					margin-right: rem(55);
					margin-bottom: rem(55);

					display: flex;
					flex-direction: column;
					align-items: center;

					@include min1600 {
						flex: 0 0 rem(60);
						width: rem(60);
						height: rem(60);
					}
					
					@include min1340 {
						width: rem(60);
						margin-right: rem(30);
						margin-bottom: rem(30);
					}

					@include min540 {
						flex: 0 0 calc((100% - 45px) / 4);
						width: calc((100% - 45px) / 4);
						margin-right: rem(15);

						&:last-child {
							margin-bottom: 0;
						}
					}

					&:nth-child(4n) {
						// margin-right: 0;
						@include min540 {
							margin-right: 0;
						}
					}

					&:nth-child(5n) {
						margin-right: 0;

						@include min1340 {
							margin-right: rem(30);
						}

						@include min540 {
							margin-right: rem(15);
						}
					}

					img {
						border-radius: rem(10);

						width: 100%;
						height: 100%;
						object-fit: cover;
					}
					
					.border {
						border-radius: rem(10);
						width: 100%;
						height: 100%;
						position: absolute;
						top: 0;
						left: 0;
						border: rem(2) solid var(--c-green-global);
						transition: var(--transition);
						opacity: 0;
					}

					.text {
						margin-top: rem(10);
						font-size: rem(12);
						color: #828282;
						font-weight: bold;
						white-space: nowrap;
						text-transform: uppercase;

						@include min540 {
							font-size: rem(8);
							margin-top: rem(6);
						}
					}

					input {
						display: none;

						&:checked {
							~ .check {
								opacity: 1;
							}

							~ .border {
								opacity: 1;
							}
						}
					}

					.check {
						transition: var(--transition);
						position: absolute;
						top: rem(-11);
						right: rem(-11);
						z-index: 15;
						opacity: 0;
					}
				}

				.item {
					position: relative;
					cursor: pointer;
					box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);

					display: flex;
					flex-wrap: wrap;
					align-items: center;

					width: rem(215);
					// height: rem(50);

					border-radius: rem(10);


					margin-right: rem(30);

					@include min1050 {
						width: auto;
					}

					@include min540 {
						margin-right: rem(10);
						width: calc((100% - 10px) / 2);
						justify-content: center;
					}

					&:hover {
						.text {
							background-color: var(--c-green);

							&__icon {
								.icon_1 {
									path {
										fill: var(--c-green);
										stroke: white;
									}
								}

								.icon_2 {
									path {
										stroke: white;
									}
								}

								.icon_3 {
									path {
										fill: white;
									}
								}

								.icon_4 {
									path {
										fill: white;
									}
								}

								.icon_5 {
									path {
										fill: white;
									}
								}
							}

							&__title {
								color: white;

								span {
									color: white;
								}
							}
						}
					}

					&:last-child {
						margin-right: 0;
					}

					input {
						display: none;

						&:checked {

							~ .check {
								opacity: 1;
							}

							~ .text {
								background-color: var(--c-green);

								.text__icon {
									.icon_1 {
										path {
											fill: var(--c-green);
											stroke: white;
										}
									}

									.icon_2 {
										path {
											stroke: white;
										}
									}

									.icon_3 {
										path {
											fill: white;
										}
									}

									.icon_4 {
										path {
											fill: white;
										}
									}

									.icon_5 {
										path {
											fill: white;
										}
									}
								}

								.text__title {
									color: white;

									span {
										color: white;
									}
								}
							}
						}
					}

					.check {
						transition: var(--transition);
						position: absolute;
						top: rem(-11);
						right: rem(-11);

						opacity: 0;
					}

					.text {
						border-radius: rem(10);
						background-color: white;

						display: flex;
						align-items: center;
						// flex-wrap: wrap;
						width: 100%;
						height: rem(60);

						padding: 0 rem(15);

						transition: var(--transition);

						@include min540 {
							height: rem(50);
							padding: 0 rem(12);
						}

						&__icon {
							flex: 0 0 rem(30);
							width: rem(30);
							// height: rem(30);
							margin-right: rem(15);

							@include min540 {
								margin-right: rem(10);
								flex: 0 0 rem(20);
								width: rem(20);
							}

							svg {
								path {
									transition: var(--transition);
								}
							}

							.icon_1 {
								path {
									fill: white;
									stroke: var(--c-green);
								}

								@include min540 {
									width: 20px !important;
									height: 20px !important;
								}
							}

							.icon_2 {
								path {
									stroke: var(--c-green);
								}

								@include min540 {
									width: 20px !important;
									height: 20px !important;
								}
							}

							.icon_3 {
								path {
									fill: #BDBDBD;
								}

								@include min540 {
									width: 20px !important;
									height: 20px !important;
								}
							}

							.icon_4 {
								path {
									fill: #C4C4C4;
								}
								@include min540 {
									width: 20px !important;
									height: 20px !important;
								}
							}

							.icon_5 {
								path {
									fill: #4F4F4F;
								}

								@include min540 {
									width: 20px !important;
									height: 20px !important;
								}
							}
						}

						&__title {
							font-size: rem(14);
							color: var(--c-black);
							font-weight: 500;
							text-transform: uppercase;

							transition: var(--transition);

							display: flex;
							flex-direction: column;
							white-space: nowrap;

							@include min540 {
								font-size: rem(8);
							}

							> span {
								transition: var(--transition);
								font-weight: 300;
								text-transform: none;
								color: #4F4F4F;
							}
						}
					}
				}
			}

			.selected_items.selected_items_3 {
				.item {
					width: calc((100% - 52px) / 3);
					margin-right: rem(26);

					@include min540 {
						width: calc((100% - 20px) / 2);
						margin-right: rem(20);

						&:nth-child(2n) {
							margin-right: 0;
						}
					}
					
					.text {
						justify-content: center;
					}

					&:last-child {
						margin-right: 0;

						@include min540 {
							width: 100%;
							margin-top: rem(10);

						}
					}
				}
			}

			.selected_items.cards {
				.item {
					width: calc((100% - 30px) / 2);
					height: rem(200);
					margin-bottom: rem(30);

					@include min540 {
						width: calc((100% - 10px) / 2);
						height: auto;
						margin-bottom: rem(10);
					}

					.check {
						right: rem(13);
					}

					.text {
						height: 100%;
						padding: rem(30);
						align-items: baseline;

						@include min540 {
							padding: rem(12);
						}

						&__title {
							font-size: rem(14);

							@include min540{
								font-size: rem(8);
							}
						}
					}

					&:nth-child(2n) {
						margin-right: 0;
					}
				}
			}
			
		}

		&-sub {
			margin-bottom: rem(25);
			margin-top: rem(25);

			p {
				font-size: rem(14);
				color: #828282;
				font-weight: bold;
				display: flex;
				align-items: center;

				svg {
					margin-right: rem(4);
					margin-left: rem(4);
				}

				i {
					color: var(--c-green-global);
					font-style: normal;
				}

				a {
					color: var(--c-green);
				}
			}
		}

		&-block {
			position: relative;
			box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
			border-radius: rem(20);
			border: rem(2) solid var(--c-yellow);

			padding: rem(30);

			display: flex;
			flex-direction: column;
			// overflow: hidden;

			@include min1050 {
				padding: rem(15);
				margin-bottom: rem(84);
			}

			@include min540 {
				margin-bottom: rem(15);
			}

			&::before {
				content: "";
				position: absolute;
				right: rem(-1);
				bottom: 0;
				background: url(../img/logo_two.png) no-repeat;
				width: rem(227);
				height: rem(102);

				@include min1050 {
					display: none;
				}
			}


			&::after {
				content: "";
				position: absolute;
				top: rem(22);
				left: rem(-22);
				background: url(../img/gift.png) no-repeat;
				width: rem(46);
				height: rem(46);
				z-index: 2;

				@include min1050 {
					display: none;
				}
			}

			.top {
				display: flex;
				flex-direction: column;
				margin-bottom: rem(38);

				&__title {
					font-weight: 500;
					font-size: rem(24);
					margin-bottom: rem(4);

					span {
						color: var(--c-green);
					}
				}

				&__descr {
					color: var(--c-gray);
					font-size: rem(14);
					max-width: rem(398);
				}
			}

			.bottom {
				display: flex;
				flex-wrap: wrap;

				@include min1050 {}

				&__left {
					display: flex;
					flex-direction: column;

					margin-right: rem(24);

					b {
						color: var(--c-gray);
						font-size: rem(10);
						letter-spacing: rem(2);
						text-transform: uppercase;
						margin-bottom: rem(3);
					}

					> span {
						font-size: rem(30);
						color: var(--c-green);

						span {
							color: var(--c-green);
							font-size: rem(18);
							font-weight: 300;
						}
					}
				}

				&__right {
					flex: 0 0 rem(148);
					max-width: rem(148);

					p {
						color: var(--c-bgray);
						font-size: rem(12);
					}
				}
			}
		}

		&-info {
			display: flex;
			flex-direction: column;
			margin-bottom: rem(100);

			@include min {
				margin-bottom: rem(20);
			}

			@include min {
				display: none;
			}

			

			.info__item {
				display: flex;
				// flex-wrap: wrap;
				margin-bottom: rem(27);

				&:last-child {
					margin-bottom: 0;
				}

				&-image {
					flex: 0 0 rem(30);
					width: rem(30);
					height: rem(30);
					margin-right: rem(30);
				}

				&-text {
					display: flex;
					flex-direction: column;

					b.flex-row {
						flex-direction: row;

						span {
							margin-left: rem(3);
						}
					}

					b {
						font-size: rem(14);
						color: var(--c-black);
						font-weight: 500;
						margin-bottom: rem(15);

						display: flex;
						flex-direction: column;

						> span {
							font-weight: 300;
							color: #4F4F4F;
						}
					}

					p {
						font-size: rem(15);
						line-height: 180%;
						color: var(--c-gray);
					}
				}
			}
		}

		
	}

	&__left.step-6 {
		.selected_items {
			margin-bottom: rem(10);
		}
	}

	&__right {
		position: relative;
		background-color: #F8F8F8;
		border-radius: rem(20);
		padding: rem(50);

		flex: 0 0 55%;
		max-width: 55%;

		height: rem(740);

		display: flex;
		flex-direction: column;

		@include min1340 {
			flex: 0 0 50%;
			max-width: 50%;
			padding: rem(30);
		}

		@include min1050 {
			flex: 0 0 50%;
			max-width: 50%;
			height: auto;
		}

		@include min {
			flex: 0 0 100%;
			max-width: 100%;
			padding: 0;
			background-color: transparent;

			display: flex;
			justify-content: center;

			margin-top: rem(40);
			margin-bottom: rem(30);
		}

		

		.title {
			font-size: rem(18);
			font-weight: 500;
			color: var(--c-black);
			text-transform: uppercase;
			margin-bottom: rem(24);

			margin-left: rem(50);

			@include min1600 {
				margin-left: rem(20);
			}

			@include min1340 {
				display: none;
			}
		}

		> ul {
			display: flex;
			flex-direction: column;

			margin-left: rem(50);

			@include min1600 {
				margin-left: rem(20);
			}

			@include min1340 {
				display: none;
			}

			> li {
				margin-bottom: rem(20);

				display: flex;
				align-items: center;

				&:last-child {
					margin-bottom: 0;
				}

				> svg {
					margin-right: rem(10);
				}

				> span {
					font-weight: 500;
					color: var(--c-black);
				}
			}

			li.hide {
				display: none;
			}

			li.disabled {
				span {
					color: #CFD0CF;
				}
			}

			li.opacity-6 {
				opacity: .6;
			}
		}
		
		&-price {
			margin-left: rem(50);
			margin-bottom: rem(50);
			display: flex;
			flex-direction: column;
			margin-top: auto;

			@include min {
				position: absolute;
				bottom: rem(60);
				margin-left: 0;
				left: 0;
			}

			.top {
				margin-bottom: rem(5);
				font-size: rem(12);
				font-weight: bold;
				color: #BDBDBD;
				text-transform: uppercase;

				@include min540 {
					font-size: rem(10);
					max-width: rem(62);
				}
			}

			.bottom {
				font-weight: bold;
				font-size: rem(24);
				color: var(--c-black);

				@include min540 {
					font-size: rem(18);
				}
			}
		}


		.calc_last_slider {
			position: absolute;
			top: 0;
			right: 0;
			width: 100%;
			height: 100%;
			z-index: 12;
			background-color: #F8F8F8;
			border-radius: rem(20);
			padding: rem(54) rem(66) 0 rem(66);
			display: none;

			@include min {
				display: none;
			}

			.swiper-nav {
					position: absolute;
					top: 50%;
					left: 0;
					height: 0;
					width: 100%;
					z-index: 13;

					&__prev, &__next {
						position: absolute;
						top: 0;
						display: flex;
						align-items: center;
						justify-content: center;
						background-color: white;
						border-radius: 50%;
						width: rem(60);
						height: rem(60);
						box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
					}

					&__prev {
						left: -30px;
					}

					&__next {
						right: -30px;
					}
				}

			.swiper-container {
				// height: 100%;
				.swiper-slide {
					opacity: 0 !important;
					pointer-events: visible !important;
				}

				

				.swiper-slide-active, .swiper-slide-next {
					opacity: 1 !important;
				}
			}
		}

		.calc_last_slider.active {
			display: block;

			@include min {
				display: none;
			}
		}

		
	}

	@keyframes opacityAnim {
		0% {
			opacity: 0;
		}

		100% {
			opacity: 1;
		}
	}

	.image-loop-animate {
		animation: opacityAnim 2s ease-in-out;
	}

	.image-loop {
		position: absolute !important;
		width: rem(680);
		// height: rem(700);

		top: 30px;
		right: -22px;
		z-index: 10;

		@include min1050 {
			width: rem(550);
		}

		@include min540 {
			margin-bottom: rem(30);
		}

		.js-image-change-2-1,
		.js-image-change-2-2 {
			display: none;
		}

		.image-loop-container.step4-2 {
			.js-image-change-2 {
				display: none;
			}
			.js-image-change-2-1 {
				display: block;
			}
			.js-image-change-2-2 {
				display: none;
			}
		}

		.image-loop-container.step5-2 {
			.js-image-change-2 {
				display: none;
			}

			.js-image-change-2-1 {
				display: none;
			}

			.js-image-change-2-2 {
				display: block;
			}
		}

		.js-image-zoom__zoomed-image {
			.image-loop-container {
				position: relative;

				.type2_1 {
					bottom: rem(145.5);
					right: rem(10);

					width: rem(260.5) !important;
					height: rem(90.5) !important;
				}
			}

			.image-loop-container.step4-2 {
				.type2_1 {
					bottom: rem(144);
					right: rem(-14);

					width: rem(280) !important;
					height: rem(85) !important;
				}
			}
		}

		

		@include min1600 {
			right: rem(-42);
		}

		@include min {
			position: relative !important;
			width: 100%;
			height: auto;
			top: auto;
			right: auto;

			display: flex;
			justify-content: center;

			.image-loop-container {
				display: flex;
				width: 100%;
				justify-content: center;
			}
		}

		@include min {
			width: 100%;
			position: absolute !important;
			top: 0;
			left: 0;
			z-index: -1;
		}

		@include min540 {
			// width: 60%;
			margin-left: auto;
			margin-right: auto;
			height: auto;

			img {
				width: 100% !important;
				height: auto !important;
			}
		}

		.type2_1 {
			position: absolute;
			bottom: rem(250);
			right: rem(73);

			width: rem(349);
			height: rem(159);
		}

		.step4-2 {
			.type2_1 {
				width: rem(328);
				height: rem(149);
				bottom: rem(191);
				right: rem(57);
			}
		}

		.js-image-zoom__zoomed-area {
			display: none !important;
		}

		.js-image-zoom__zoomed-image {
			position: absolute !important;
			bottom: 0 !important;
			left: rem(-272) !important;
			top: auto !important;
			right: auto !important;

			// transform: scale(2);

			display: block !important;
			width: rem(250) !important;
			height: rem(250) !important;
			border-radius: 50% !important;
			border: rem(2) solid #BCBCBC !important;
			// background-position: -172px -347px;
			overflow: hidden;

			.image-loop-container {
				transform: scale(2);
			}

			@include min1600 {
				left: rem(-220) !important;
			}

			@include min1340 {
				display: none !important;
			}
		}
	}

	.image-loop.disabled {
		display: none;

		@include min {
			display: block;
		}
	}

	.swiper-nav {
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 10;

		display: flex;
		flex-wrap: wrap;
		align-items: center;

		@include min {
			position: relative;
			bottom: auto;
			left: auto;
			// margin-top: rem(340);
		}

		@include min540 {
			flex-wrap: nowrap;
			order: 10;
		}

		&_prev {
			width: rem(60);
			height: rem(60);
			border-radius: 50%;
			background-color: var(--c-white-light);
			

			display: flex;
			align-items: center;
			justify-content: center;

			margin-right: rem(20);

			@include min540 {
				flex: 0 0 rem(50);
				width: rem(50);
				height: rem(50);
				margin-right: rem(10);
			}

			
		}

		&_next {
			align-items: center;
			height: rem(60);

			margin-right: rem(28);

			@include min540 {
				margin-right: 0;
				width: 100%;
				justify-content: center;
				height: rem(50);
				flex: 1;
			}

			span {
				margin-right: rem(5);

				@include min540 {
					font-size: rem(14);
					white-space: nowrap;
				}
			}

			.icon.hide {
				display: none;
			}
		}

		&_next.disabled {
			display: none;
		}

		&_text {
			font-size: rem(14);
			font-weight: bold;
			color: var(--c-black);
			letter-spacing: rem(2);

			@include min540 {
				display: none;
			}
		}

		&_text.hide {
			display: none;
		}

		.swiper-slider-first {
			display: flex;
			align-items: center;
		}

		.swiper-slider-first.disabled {
			display: none;
		}

		.modal_message_active {
			display: none;
			height: 3.75rem;
		}

		.modal_message_active.active {
			display: block;
		}

		

		.button_smile {
			margin-left: rem(28);

			height: rem(60);

			display: flex;
			align-items: center;
			border-radius: rem(60);
			border: rem(2) solid var(--c-yellow);
