		}

		.button_smile.hide {
			display: none;
		}

		.swiper-nav_prev.swiper-button-disabled {
			display: none;
		}
	}

	.swiper-slider-nav-i {
			display: none;
			align-items: center;
			margin-left: rem(30);

		@include min540 {
			margin-left: 0;
			position: absolute;
			bottom: rem(70);
			right: 0;
		}

			span {
				font-size: rem(18);
				text-transform: uppercase;
				color: #828282;
				letter-spacing: rem(1);
				font-weight: 500;

				display: flex;
				align-items: center;

				@include min540 {
					font-size: rem(12);
