.sect_article {
	margin-bottom: rem(92);

	@include min1050 {
		margin-bottom: 40px;
	}

	@include min768 {
		display: none;
		margin-bottom: rem(28);
	}

	&__title {
		margin-bottom: rem(60);
		display: flex;
		align-items: center;

		&-link {
			margin-left: rem(30);
			display: flex;
			align-items: center;

			span {
				font-size: rem(14);
				margin-left: 14px;
				color: var(--c-green);
			}
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item_article {
			flex: 0 0 calc((100% - 90px) / 4);
			max-width: calc((100% - 90px) / 4);
			margin-right: 30px;

			@include min1050 {
				flex: 0 0 calc((100% - 30px) / 2);
				max-width: calc((100% - 30px) / 2);
				margin-bottom: 30px;

				&:nth-child(2n) {
					margin-right: 0;
				}
			}

			&:last-child {
				margin-right: 0;

				@include min768 {
					margin-bottom: 0;
				}
			}

			@include min768 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-right: 0;
				

			}
		}
	}
}

.item_article {
	display: flex;
	flex-direction: column;

	&:hover {
		.item_article__title {
			color: var(--c-green-global);
		}
	}

	&__image {
		height: rem(230);
		border-radius: 30px;
		margin-bottom: 20px;
		overflow: hidden;

		@include min540 {
			height: rem(175);

		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		margin-bottom: 12px;
		font-size: rem(18);
		font-weight: 700;
		color: var(--c-black);
		transition: 250ms ease;
		font-family: 'Roboto', sans-serif;

		@include min540 {
			margin-bottom: rem(10);
		}
	}

	&__text {
		font-size: rem(14);
		line-height: rem(24);
		color: var(--c-gray);
		font-family: 'Roboto', sans-serif;
	}
}