.sect_video {
	margin-bottom: rem(148);
	background-color: #F8F8F8;
	padding-top: rem(60);
	padding-bottom: rem(148);

	@include min768 {
		margin-bottom: rem(376);
		padding-bottom: rem(78);

		padding-top: rem(20);
	}

	>.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
	}

	.link {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		margin-top: rem(24);

		&:hover {
			span {
				color: var(--c-yellow);
			}
		}

		img {
			margin-right: rem(20);
		}

		span {
			transition: var(--transition);
			font-size: rem(14);
			font-weight: bold;
			color: var(--c-black);
			text-transform: uppercase;
			max-width: rem(184);
		}
	}

	&__left {
		display: flex;
		flex-direction: column;

		flex: 0 0 rem(294);
		max-width: rem(294);

		margin-right: rem(30);

		@include min1340 {
			flex: 0 0 50%;
			max-width: 50%;

			margin-right: 0;

			padding-right: rem(30);
		}

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
			padding-right: 0;
			margin-bottom: rem(30);
		}

		@include min768 {
			.title-36 {
				font-size: rem(24);
			}
		}

		p {
			font-size: rem(15);
			line-height: rem(24);
			color: var(--c-gray);

			

			&:first-of-type {
				margin-top: rem(14);
			}

			margin-bottom: rem(28);

			&:last-of-type {
				margin-bottom: 0;
			}

			@include min540 {
				font-size: rem(14);
				margin-bottom: rem(24);
			}
		}

		

		.desktop {
			@include min540 {
				display: none;
			}
		}
	}

	&__center {
		position: relative;
		flex: 1;

		border-radius: rem(20);
		overflow: hidden;

		@include min1340 {
			flex: 0 0 50%;
			max-width: 50%;
		}

		@include min1050 {
			flex: 0 0 100%;
			max-width: 100%;
		}

		.play {
			position: absolute;
			top: rem(80);
			left: rem(80);

			display: flex;
			align-items: center;

			@include min600 {
				top: rem(15);
				left: rem(15);
			}

			span {
				margin-left: rem(30);
				font-size: rem(14);
				font-weight: bold;
				color: var(--c-black);
				max-width: rem(137);
				text-transform: uppercase;
				text-align: left;

				@include min540 {
					margin-left: rem(10);
					font-size: rem(12);
					max-width: rem(121);
				}
			}
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__right {
		margin-left: rem(55);

		flex: 0 0 rem(380);
		max-width: rem(380);

		display: flex;
		flex-direction: column;

		@include min1600 {
			margin-left: rem(30);
			flex: 0 0 rem(280);
			max-width: rem(280);
		}

		@include min1340 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-left: 0;
			margin-top: rem(32);
		}

		.smart {
			display: none;

			@include min540 {
				display: flex;
				margin-top: 0;
				margin-bottom: rem(30);
			}
		}

		.item {
			display: flex;
			// flex-wrap: wrap;
			margin-bottom: rem(46);

			@include min540 {
				margin-bottom: rem(20);
			}

			&:last-child {
				margin-bottom: 0;
			}

			&__left {
				flex: 0 0 rem(60);
				width: rem(60);
				height: rem(60);

				display: flex;
				align-items: center;
				justify-content: center;

				margin-right: rem(32);

				@include min1600 {
					margin-right: rem(16);
				}

				@include min540 {
					flex: 0 0 rem(40);
					width: rem(40);
					height: rem(40);
					margin-right: rem(20);
				}
			}

			&__right {
				display: flex;
				flex-direction: column;

				b {
					font-size: rem(18);
					line-height: rem(24);
					color: var(--c-bgray);
					margin-bottom: rem(5);

					@include min540 {
						font-size: rem(13);
					}
				}

				p {
					font-size: rem(15);
					line-height: rem(24);
					color: var(--c-gray);

					@include min540 {
						font-size: rem(13);
						line-height: rem(20);
					}
				}
			}

		}
	}

	.form_questions {
		flex: 0 0 100%;
		width: 100%;
		max-width: 100%;

		position: absolute;
		left: 0;
		bottom: rem(-220);

		@include min1340 {
			bottom: rem(-260);
		}

		@include min768 {
			bottom: rem(-390);
		}
	}
}