.footer {
	background-color: #333333;
	display: flex;
	flex-direction: column;

	&__top {
		position: relative;
		padding-bottom: rem(80);

		> .container {
			position: relative;
			@include min768 {
				display: flex;
				flex-direction: column;
			}
		}

		@include min1340 {
			padding-bottom: rem(115);
		}

		@include min768 {
			padding-bottom: 0;
			background-color: #494949;

			// height: rem(510);
			
		}

		.form_questions {
			position: absolute;
			top: rem(-80);
			width: 100%;

			@include min1340 {
				top: rem(-115);
				// width: calc(100% - 40px);
			}

			@include min768 {
				position: relative;
				top: auto;
				width: 100%;
				top: rem(-30);
			}
		}

		.smart {
			display: none;

			@include min768 {
				display: flex;
				flex-direction: column;
				align-items: center;
			}

			p {
				font-size: rem(14);
				color: white;
				text-align: center;
				margin-bottom: rem(17);
			}

			.socilal_links {
				display: flex;
				flex-wrap: wrap;

				margin-bottom: rem(25);

				a {
					width: rem(40);
					height: rem(40);
					border-radius: 50%;
					display: flex;
					align-items: center;
					justify-content: center;
					margin-right: rem(30);
					background-color: #333333;

					svg {
						width: rem(18) !important;
						height: rem(18) !important;
						path {
							fill: white;
						}
					}

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}
	}

	&__bottom {
		padding: rem(25) rem(100);

		@include min1600 {
			padding: rem(25) rem(40);
		}

		@include min768 {
			padding: rem(25) 0;
		}

		> .container {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;
			
			padding-left: rem(110);
			padding-right: rem(80);

			@include min1440 {
				padding-left: 0;
				padding-right: 0;
			}
			

			@include min1200 {
				flex-direction: column;
			}

			@include min768 {
				// margin: 0;
				padding: 0;
			}
		}

		

		.logo {
			max-width: rem(143);

			margin-right: rem(58);

			@include min1600 {
				margin-right: rem(24);
			}

			@include min1200 {
				margin-right: 0;
				margin-bottom: rem(24);
			}

			@include min768 {
				order: 9999;
				margin-bottom: 0;
			}
		}

		.copy {
			font-size: rem(14);
			color: #F8F8F8;
			line-height: 150%;

			margin-right: rem(50);

			@include min1600 {
				margin-right: rem(24);
			}

			@include min1200 {
				margin-right: 0;
				margin-bottom: rem(24);
				text-align: center;
			}

			@include min768 {
				order: 3;
				text-align: left;
				align-self: flex-start;
			}
		}

		.list {
			margin-right: rem(34);

			@include min1600 {
				margin-right: rem(24);
			}

			@include min1200 {
				margin-right: 0;
				margin-bottom: rem(24);
			}

			@include min768 {
				order: 2;
				align-self: flex-start;
			}

			ul {
				display: flex;
				flex-direction: column;

				@include min1200 {
					align-items: center;
				}

				@include min768 {
					align-items: flex-start;
				}

				li {
					margin-bottom: rem(4);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						font-size: rem(14);
						color: #F8F8F8;

						@include min768 {

						}

						&:hover {
							text-decoration: underline;
						}
					}
				}
			}
		}

		.socilal_links {
			display: flex;
			flex-wrap: wrap;
			margin-right: rem(32);

			@include min1600 {
				margin-right: rem(14);
			}

			@include min1200 {
				margin-right: 0;
				margin-bottom: rem(24);
			}

			@include min768 {
				display: none;
			}

			a {
				width: rem(25);
				height: rem(25);
				border-radius: 50%;
				background-color: var(--c-bgray);

				display: flex;
				align-items: center;
				justify-content: center;

				margin-right: rem(10);

				&:last-child {
					margin-right: 0;
				}
			}
		}
		
		.block_text_right {
			margin-right: rem(28);

			@include min1600 {
				margin-right: rem(20);
			}

			@include min1200 {
				margin-right: 0;
				margin-bottom: rem(24);
			}

			@include min768 {
				flex-direction: row-reverse;
			}

			&:last-child {
				margin-right: 0;
			}

			.text {
				span {
					color: var(--c-gray);

					@include min768 {
						font-size: rem(14);
					}
				}

				b {
					color: white;

					@include min768 {
						font-size: rem(18);
					}
				}

				@include min768 {
					align-items: flex-start;
				}
			}

			.image {
				svg {
					path {
						fill: white;
					}
				}

				@include min768 {
					margin-right: rem(22);
				}
			}
		}

		.block_text_right.first {
			@include min768 {
				order: 1;
			}
			
		}

		.block_text_right.last {
			@include min768 {
				order: -9999;
			}

		}
	}
}