.block_green_overlay {
	position: relative;
	height: rem(190);
	width: 100%;
	border-radius: rem(30);

	padding: rem(36) rem(70);

	background: url(../img/block_green_overlay.jpg) no-repeat;
	background-size: cover;
	box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);

	overflow: hidden;

	display: flex;
	flex-wrap: wrap;
	align-items: center;

	@include min1340 {
		padding: rem(30) rem(40);
	}

	@include min768 {
		height: auto;

		flex-direction: column;
	}

	@include min540 {
		padding: rem(20);
	}

	

	.count {
		font-size: rem(96);
		font-weight: 800;
		color: #FFD700;

		margin-right: rem(40);

		@include min1600 {
			font-size: rem(76);
		}

		@include min768 {
			margin-right: 0;
			margin-bottom: rem(20);
		}

		@include min540 {
			font-size: rem(72);
		}
	}

	.title {
		font-size: rem(36);
		color: white;
		margin-right: rem(30);

		@include min1600 {
			font-size: rem(24);
		}

		@include min768 {
			margin-right: 0;
			margin-bottom: rem(10);

			br {
				display: none;
			}
		}

		@include min540 {
			margin-left: auto;
			margin-right: auto;
			font-size: rem(18);
		}

		@include min340 {
			margin-left: 0;
		}
	}

	.descr {
		max-width: rem(300);
		font-size: rem(18);
		line-height: 140%;
		color: white;

		@include min1600 {
			font-size: rem(15);
		}

		@include min540 {
			font-size: rem(14);
		}
	}

	.button_yellow {
		margin-left: auto;
		margin-right: rem(230);

		height: rem(50);
		background-color: #FFD700;

		display: flex;
		align-items: center;

		box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);

		@include min1340 {
			margin-right: rem(160);
		}

		@include min1200 {
			margin-right: auto;
			position: relative;
			z-index: 2;
		}

		@include min768 {
			margin-right: auto;
			margin-top: rem(32);
		}

		@include min540 {
			margin-top: rem(90);
			position: relative;
			z-index: 2;
		}
	}

	img {
		position: absolute;
		bottom: 0;
		right: rem(10);

		@include min540 {
			width: 100%;
		}
	}

	&-col {
		display: flex;
		flex-direction: column;

		.title {
			margin-right: 0;
			margin-bottom: rem(10);
			font-size: rem(48);

			@include min1600 {
				font-size: rem(32);
			}
		}

		.descr {
			max-width: rem(500);
		}
	}

}