.button_green {
	padding: rem(16) rem(40);
	border-radius: rem(50);

	display: flex;

	background-color: var(--c-green);

	transition: var(--transition);

	@include min1600 {
		padding: rem(14) rem(30);
	}

	&:hover {
		background-color: var(--c-green-global);
	}

	span {
		color: white;
		font-size: rem(14);
		font-weight: bold;
		text-transform: uppercase;

		@include min1600 {
			font-size: rem(12);
		}
	}
}

.button_yellow {
	padding: rem(22) rem(50);
	background-color: var(--c-yellow-light);
	border-radius: rem(50);
	display: flex;

	transition: var(--transition);

	@include min1600 {
		padding: rem(16) rem(30);
	}

	&:hover {
		background-color: var(--c-yellow);
	}

	span {
		color: var(--c-black);
		font-size: rem(14);
		font-weight: bold;
		text-transform: uppercase;

		@include min1600 {
			font-size: rem(12);
		}
	}
}

.button_play {
	display: flex;
	align-items: center;

	&:hover {
		.image {
			svg {
				path {
					fill: var(--c-yellow);
				}
			}
		}

		span {
			color: var(--c-yellow);
		}
	}

	.image {
		margin-right: rem(20);

		display: flex;
		align-items: center;
		justify-content: center;

		width: rem(60);
		height: rem(60);
		border-radius: 50%;
		background-color: #0F5A2F;

		@include min1600 {
			width: rem(47);
			height: rem(47);

			margin-right: rem(10);
		}

		svg {
			margin-left: rem(4);
			path {
				transition: var(--transition);
				fill: white;
			}
		}
	}

	span {
		font-size: rem(12);
		font-weight: bold;
		color: white;
		text-transform: uppercase;

		transition: var(--transition);
	}
}

.social__links {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	a {
		width: rem(25);
		height: rem(25);
		border-radius: 50%;
		background-color: var(--c-white-light);

		display: flex;
		align-items: center;
		justify-content: center;

		margin-right: rem(10);

		&:last-child {
			margin-right: 0;
		}

		&:hover {
			svg {
				path {
					fill: var(--c-green-global);
				}
			}
		}

		svg {
			path {
				transition: var(--transition);
				fill: var(--c-bgray);
			}
		}
	}
}


.block_text_right {
	display: flex;
	flex-wrap: wrap;

	&:hover {
		.text {
			b {
				color: var(--c-green-global);
			}
		}

		.image {
			svg {
				path {
					fill: var(--c-green-global);
				}
			}
		}
	}

	.text {
		margin-right: rem(10);

		display: flex;
		flex-direction: column;
		align-items: flex-end;

		span {
			color: var(--c-gray);
			font-size: rem(14);

			@include min1600 {
				font-size: rem(12);
			}
		}

		b {
			color: #312F30;
			font-weight: bold;

			@include min1600 {
				font-size: rem(14);
			}
		}

	}

	.image {
		margin-top: rem(4);
		svg {
			path {
				transition: var(--transition);
				fill: var(--c-black);
			}
		}
	}

	
}


.button_header_smart {
	width: rem(50);
	height: rem(50);
	border-radius: 50%;
	background-color: var(--c-green);

	display: flex;
	align-items: center;
	justify-content: center;
}

.slider-nav {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	justify-content: space-between;

	width: rem(95);
}
.slider-prev, .slider-next {
	width: rem(40);
	height: rem(40);
	border-radius: 50%;
	background-color: white;
	display: flex;
	align-items: center;
	justify-content: center;
	box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);
	

	svg {
		path {
			fill: #27B985;
		}
	}
}

.mt-auto {
	margin-top: auto;
}

.mt-0 {
	margin-top: 0;
}

.mt-40 {
	margin-top: rem(40);
}
.mb-40 {
	margin-bottom: rem(40);
}


.field-checked {
	display: flex;
	align-items: center;

	input {
		display: none;

		&:checked {
			~ .field-checked__check {
				background-color: var(--c-green-global);

				svg {
					opacity: 1;
				}
			}
		}
	}

	&__check {
		width: rem(16);
		height: rem(16);
		border-radius: rem(2);
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: white;
		border: rem(1) solid var(--c-gray-light);
		margin-right: rem(9);

		svg {
			opacity: 0;
		}
	}

	&__text {
		font-size: rem(12);
		font-weight: 500;
		line-height: 160%;
		color: #7B818C;
	}
}

.number-counter {
	position: relative;
	max-width: rem(106);
	display: flex;
	align-items: center;

	&__field {
		border: rem(1) solid #E0E0DD;
		height: rem(50);
		width: rem(76);
		border-radius: rem(20);
		background-color: white;
		text-align: center;

		font-size: rem(18);
		font-weight: bold;
		color: #45494E;

		appearance: none;

		&::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0;
		}
	}

	&__button {
		position: absolute;
		top: 50%;
		z-index: 2;
		transform: translateY(-50%);
		flex: 0 0 rem(30);
		width: rem(30);
		height: rem(30);
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;

		font-size: rem(18);
		font-weight: bold;
		transition: 250ms ease;
	}

	&__button.minus {
		color: #45494E;
		background-color: white;
		box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.1);
		left: rem(-15);

		&:hover {
			background-color: var(--c-green-global);

			color: white;
		}
	}
	&__button.plus {
		background-color: var(--c-green-global);
		color: white;
		right: rem(-15);

		&:hover {
			background-color: var(--c-green);
		}
	}
}

.button_scroll_top {
	position: fixed;
	z-index: 50;
	bottom: 40px;
	right: 40px;

	width: rem(60);
	height: rem(60);
	border-radius: 50%;
	background-color: white;
	display: none;
	align-items: center;
	justify-content: center;
	box-shadow: 0px 20px 40px rgba(12, 87, 60, 0.05);

	transition: 250ms ease;

	@include min1050 {
		display: none;
	}

	&:hover {
		background-color: var(--c-green-global);

		svg {
			path {
				fill: white;
			}
		}
	}
}

.button_scroll_top.active {
	display: flex;
}