.page_catalog {

	margin-bottom: rem(70);

	.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&__top {
		position: absolute;
		top: 0;
		left: 408px;

		@include min1600 {
			left: 310px;
		}

		@include min1340 {
			position: relative;
			left: 0;
			flex: 0 0 100%;
			max-width: 100%;
			margin-bottom: rem(30);
		}

		@include min768 {
			margin-bottom: rem(20);
		}

		.title-h1 {
			font-size: rem(36);
			color: #333333;

			@include min768 {
				font-size: rem(24);
			}
		}
	}

	&__left {
		flex: 0 0 378px;
		max-width: 378px;
		margin-right: rem(30);

		// align-self: baseline;

		// position: sticky;
		// top: 20px;

		@include min1600 {
			margin-right: rem(20);
			flex: 0 0 rem(290);
			max-width: rem(290);
		}

		@include min1340 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;
			margin-bottom: rem(30);
		}

		.aside_blog {
			.desktop {
				@include min1340 {
					display: none;
				}
			}

			.mobile {
				display: none;

				@include min1340 {
					display: flex;
					align-items: center;
					justify-content: space-between;
					width: 100%;

					span {
						font-size: rem(14);
						color: white;
						text-transform: uppercase;
						font-weight: bold;
					}
				}
			}

			&__title {

				@include min1340 {
					margin-bottom: 0;
					font-size: rem(14);
				}
			}

			&__list {

				@include min1340 {
					display: none;
				}
			}

			&__list.active {
				display: flex;
				margin-top: rem(22);
			}
		}
	}

	&__right {
		flex: 1;
		margin-top: rem(100);
		
		@include min1340 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-top: 0;
		}

		.filter_mobile_button {
			display: none;
			background-color: #F8F8F8;
			border-radius: rem(50);
			height: rem(60);
			align-items: center;
			justify-content: center;
			width: 100%;
			margin-bottom: rem(20);
			box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);

			span {
				font-size: rem(14);
				font-weight: bold;
				text-transform: uppercase;
				margin-right: rem(12);
				color: #2F2F2F;
			}



			@include min1340 {
				display: flex;
			}
		}

		.filter_bar {
			margin-bottom: rem(40);

			@include min768 {
				margin-bottom: rem(30);
			}
		}

		&-items {
			display: flex;
			flex-wrap: wrap;

			.item_product {
				flex: 0 0 calc((100% - 60px) / 3);
				max-width: calc((100% - 60px) / 3);
				margin-right: rem(30);
				margin-bottom: rem(60);

				@include min1600 {
					flex: 0 0 calc((100% - 40px) / 3);
					max-width: calc((100% - 40px) / 3);
					margin-right: rem(20);

					margin-bottom: rem(40);
				}

				@include min880 {
					flex: 0 0 calc((100% - 20px) / 2);
					max-width: calc((100% - 20px) / 2);

					&:nth-child(2n) {
						margin-right: 0;
					}
				}

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-right: 0;
				}

				@include min540 {
					flex: 0 0 calc(100% + 40px);
					max-width: calc(100% + 40px);

					&:last-child {
						margin-bottom: 0;
					}
				}

				@include max880 {
					&:nth-child(3n) {
						margin-right: 0;
					}
				}

				
			}

			.block_abs {
				flex: 0 0 100%;
				max-width: 100%;
				position: relative;
				bottom: auto;
				margin-bottom: rem(40);
			}
		}

		&-pages {
			background-color: #F8F8F8;
			border-radius: rem(30);
			padding: rem(10) rem(15);
			height: rem(60);

			display: flex;
			align-self: center;
			justify-content: space-between;

			@include min768 {
				background-color: transparent;
				padding: 0;
				border-radius: 0;
				height: auto;
			}

			@include min540 {
				margin-top: rem(20);
			}

			.page_nav {
				margin-top: 0;

				&__item, &__next {
					border: 0;
					box-shadow: 0px 5px 10px rgba(11, 54, 29, 0.05);

					@include min540 {
						box-shadow: none;
						border: rem(1) solid #E0E0DD;
					}
				}

				&__next {
					background-color: white;

					&:hover {
						background-color: var(--c-green-global);

						svg {
							path {
								fill: white;
							}
						}
					}
				}
			}

			.filter_bar__select {
				position: relative;
				cursor: pointer;
				margin-left: auto;
				height: rem(40);
				background-color: white;
				border-radius: rem(30);
				padding-left: rem(17);
				padding-right: rem(8);
				box-shadow: 0px 5px 10px rgba(11, 54, 29, 0.05);

				display: flex;
				align-items: center;

				@include min768 {
					display: none;
				}

				&-name {
					margin-right: rem(10);
					font-size: rem(13);

					@include min768 {
						display: none;
					}
				}

				&-selected {
					margin-right: rem(5);
					font-size: rem(13);
					font-weight: bold;
				}

				&-list {
					position: absolute;
					top: 100%;
					left: 0;
					width: 100%;
					z-index: 10;
					// z-index: -1;
					margin-top: 0;
					border-radius: rem(30);
					background-color: white;
					box-shadow: 0px 5px 10px rgba(11, 54, 29, 0.05);

					display: none;
					flex-direction: column;

					&__item {
						height: rem(40);
						display: flex;
						align-items: center;
						justify-content: center;
						border-radius: rem(30);

						&:hover {
							background-color: var(--c-green-global);

							span, b {
								color: white;
							}
						}

						span {
							margin-right: rem(10);
							font-size: rem(13);

							@include min768 {
								display: none;
							}
						}

						b {
							font-size: rem(13);
							font-weight: bold;
						}
					}
				}
			}

			.filter_bar__select.active {
				border-top-right-radius: rem(20);
				border-top-left-radius: rem(20);
				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;

				.filter_bar__select-list {
					display: flex;
					&:first-of-type {
						border-top-left-radius: 0;
						border-top-right-radius: 0;
					}
				}
			}
		}
	}
}


.filter_bar {
	display: flex;
	align-items: center;
	background-color: #F8F8F8;
	border-radius: rem(30);
	padding: rem(10) rem(30);

	@include min768 {
		border-radius: 0;
		padding: rem(10) rem(20);
		margin-left: -20px;
		width: calc(100% + 40px);
	}

	&__text {
		margin-right: rem(26);

		@include min768 {
			display: none;
		}

		span {
			font-size: rem(14);
			font-weight: bold;
			color: var(--c-black);
			letter-spacing: rem(1);
			text-transform: uppercase;
		}
	}

	&__button {
		display: flex;
		align-items: center;

		@include min768 {
			position: relative;
			align-items: baseline;
			flex-direction: column;
			flex: 0 0 rem(190);
			max-width: rem(190);
			
			margin-right: rem(20);
		}

		&-item {
			margin-right: rem(28);
			display: flex;
			align-items: center;

			@include min768 {
				position: relative;
				z-index: 2;
				margin-right: 0;
				background-color: white;
				padding: rem(8) rem(20);
				width: 100%;
				justify-content: flex-end;
				display: none;

				span {

				}
			}

			&:hover {
				span {
					color: var(--c-green-global);
				}
			}

			&:first-child {
				@include min768 {
					display: flex;
					justify-content: flex-start;
					width: 100%;
					padding: rem(8) rem(20);
					border-radius: rem(50);
					background-color: white;
					border: rem(1) solid #EAEAEA;
				}
			}

			&:last-child {
				margin-right: 0;
				

				@include min768 {
					position: absolute;
					top: 100%;
					left: 0;
					width: 100%;
					border-bottom-right-radius: rem(20);
					border-bottom-left-radius: rem(20);
				}
			}

			svg {
				margin-right: rem(5);
				display: none;
			}

			span {
				font-size: rem(14);
				font-family: 'Roboto', sans-serif;
				color: #828282;
				transition: 250ms ease;
				white-space: nowrap;
			}
		}

		&-item.active {
			svg {
				display: block;
			}
			span {
				color: var(--c-green-global);
				font-weight: bold;
			}
		}
	}

	&__button.active {
		@include min768 {

			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: rem(42);
				background-color: white;
				border-top-left-radius: rem(20);
				border-top-right-radius: rem(20);
			}
		}

		.filter_bar__button-item {
			@include min768 {
				display: flex;
			}
		}
	}

	&__select {
		position: relative;
		cursor: pointer;
		margin-left: auto;
		height: rem(40);
		background-color: white;
		border-radius: rem(30);
		padding-left: rem(17);
		padding-right: rem(8);
		box-shadow: 0px 5px 10px rgba(11, 54, 29, 0.05);

		display: flex;
		align-items: center;

		&-name {
			margin-right: rem(10);
			font-size: rem(13);

			@include min768 {
				display: none;
			}
		}

		&-selected {
			margin-right: rem(5);
			font-size: rem(13);
			font-weight: bold;
		}

		&-list {
					position: absolute;
					top: 100%;
					left: 0;
					width: 100%;
					z-index: 10;
					// z-index: -1;
					margin-top: 0;
					border-radius: rem(30);
					background-color: white;
					box-shadow: 0px 5px 10px rgba(11, 54, 29, 0.05);

					display: none;
					flex-direction: column;

					&__item {
						height: rem(40);
						display: flex;
						align-items: center;
						justify-content: center;
						border-radius: rem(30);

						&:hover {
							background-color: var(--c-green-global);

							span, b {
								color: white;
							}
						}

						span {
							margin-right: rem(10);
							font-size: rem(13);

							@include min768 {
								display: none;
							}
						}

						b {
							font-size: rem(13);
							font-weight: bold;
						}
					}
				}
	}

	&__select.active {
		border-top-right-radius: rem(20);
				border-top-left-radius: rem(20);
				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;

				.filter_bar__select-list {
					display: flex;
					&:first-of-type {
						border-top-left-radius: 0;
						border-top-right-radius: 0;
					}
				}
	}
}


.filter_option {
	margin-top: rem(60);

	background-color: #F8F8F8;
	border-radius: rem(30);
	padding: rem(30);

	// position: sticky;
	// top: 20px;

	@include min1600 {
		margin-top: rem(30);
		padding: rem(20);
		border-radius: rem(20);
	}

	@include min1340 {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 150;
		margin-top: 0;
		overflow-x: hidden;
		overflow-y: auto;
		width: 100%;
		height: 100%;
		border-radius: 0;
		transition: 250ms ease;

		transform: translateX(100%);
	}

	display: flex;
	flex-direction: column;

	&__title {
		margin-bottom: rem(30);
		font-size: rem(18);
		font-weight: bold;
		text-transform: uppercase;
		color: #2F2F2F;
		display: flex;
		align-items: center;
		justify-content: space-between;

		&-close {
			display: none;

			@include min1340 {
				display: block;
			}
		}

		@include min1600 {
			font-size: rem(16);
			margin-bottom: rem(20);
		}
	}

	&__sect {
		display: flex;
		flex-direction: column;
		border-bottom: rem(1) solid #DFDFDF;
		margin-bottom: rem(20);

		&-toggle {
			display: none;
			flex-direction: column;
			
			padding-bottom: rem(30);

			@include min1600 {
				padding-bottom: rem(20);
			}
		}

		&-title {
			display: flex;
			align-items: center;
			justify-content: space-between;
			margin-bottom: rem(16);

			span {
				font-size: rem(16);
				font-weight: 500;
				color: #2F2F2F;
			}

			svg {
				transition: 250ms ease;
			}
		}

		&-fields {
			display: flex;
			
			&__item {
				position: relative;
				flex: 0 0 calc((100% - 10px) / 2);
				max-width: calc((100% - 10px) / 2);
				margin-right: rem(10);
				margin-bottom: rem(28);

				&:last-child {
					margin-right: 0;
				}

				.input {
					width: 100%;
					height: rem(40);
					padding: 0 rem(12) 0 rem(30);
					background-color: white;
					border: rem(1) solid #DFDFDF;
					color: #DFDFDF;
					font-size: rem(14);
					font-family: 'Roboto', sans-serif;
					border-radius: rem(30);

					appearance: none;

					transition: 250ms ease;

					&:focus {
						border-color: var(--c-green-global);
					}
					
					&::-webkit-outer-spin-button,&::-webkit-inner-spin-button {
						display: none;
						-webkit-appearance: none;
						margin: 0;
					}

				}

				label {
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					left: rem(12);
					color: #DFDFDF;
					font-size: rem(14);
					font-family: 'Roboto', sans-serif;
				}
			}
		}

		&-range {
			position: relative;
			margin-left: 8px;
			margin-right: 8px;

			.button {
				position: absolute;
				top: rem(-10);
				left: rem(-110);
				z-index: 10;

				@include min1760 {
					display: none;
				}

				span {
					position: absolute;
					top: 4px;
					left: 8px;
					color: white;
					font-size: rem(13);
					text-transform: uppercase;
					font-weight: bold;
					font-family: 'Roboto', sans-serif;
					letter-spacing: rem(0.4);
				}
			}
		}

		
		
		.colors-selected {
			display: flex;
			flex-wrap: wrap;

			.color-checked {
				margin-right: rem(5);
				margin-bottom: rem(5);

				&:last-child {
					margin-right: 0;
				}
			}
		}

		.field-checked {
			margin-bottom: rem(7);

			&:last-of-type {
				margin-bottom: 0;
			}

			input {
				&:checked {
					~ .field-checked__check {
						background-color: white;
						border-color: #DFDFDF;

						svg {
							path {
								fill: var(--c-green-global);
							}
						}
					}

					~ .field-checked__text {
						color: var(--c-green-global);
					}
				}
			}

			&__check {
				width: rem(20);
				height: rem(20);
				border-radius: rem(5);
				border: rem(1) solid #DFDFDF;
			}
		}
	}

	&__sect.first {
		padding-bottom: rem(30);

		@include min1600 {
			padding-bottom: rem(20);
		}
	}

	&__sect.active {
		.filter_option__sect-title {
			svg {
				transform: rotate(180deg);
			}
		}

		.filter_option__sect-toggle {
			display: flex;
		}
	}

	&__buttons {
			display: flex;

			@include min1340 {
				margin-top: auto;
			}

			&-submit, &-reset {
				flex: 0 0 calc((100% - 10px) / 2);
				max-width: calc((100% - 10px) / 2);
				margin-right: rem(10);
				height: rem(46);
				border-radius: rem(30);

				display: flex;
				align-items: center;
				justify-content: center;
				color: white;
				font-size: rem(14);
				letter-spacing: rem(0.5);
				font-family: 'Roboto', sans-serif;
				font-weight: bold;
				text-transform: uppercase;

				transition: 250ms ease;

				&:last-child {
					margin-right: 0;
				}
			}

			&-submit {
				background-color: var(--c-green-global);

				&:hover {
					background-color: var(--c-green);
				}
			}

			&-reset {
				background-color: #7B818C;

				&:hover {
					background-color: var(--c-gray-light);
				}
			}
		}
}

.filter_option.active {
	@include min1340 {
		transform: translateX(0%);
	}
}

.color-checked {

	cursor: pointer;

	input {
		display: none;

		&:checked {
			~ img {
				box-shadow: 0 0 0 2px #219653;
			}
		}
	}

	

	img {
		width: rem(40);
		height: rem(40);
		border-radius: rem(10);
		transition: 250ms ease;
		display: block;
	}
}