.form_questions {
	border-radius: rem(20);
	background-color: white;
	box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);

	display: flex;
	flex-wrap: wrap;
	align-items: center;

	padding: 0 rem(40);

	height: rem(160);
	
	@include min1600 {
		padding: 0 rem(30);
	}

	@include min1340 {
		height: auto;
		padding: rem(20);
	}

	form {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		width: 100%;
	}

	&__title {
		display: flex;
		flex-direction: column;

		margin-right: rem(50);

		@include min1600 {
			margin-right: rem(24);
		}

		@include min1340 {
			margin-right: 0;

			flex: 0 0 100%;
			max-width: 100%;
			align-items: center;

			margin-bottom: rem(20);
		}

		@include min768 {
			align-items: flex-start;
		}

		.title {
			font-size: rem(24);
			font-weight: bold;
			color: var(--c-black);

			@include min1440 {
				font-size: rem(20);
			}

			@include min540 {
				font-size: rem(18);
			}
		}

		p {
			margin-top: rem(6);
			font-size: rem(15);
			color: var(--c-gray);

			@include min1600 {
				font-size: rem(13);
				max-width: rem(260);
			}

			@include min1340 {
				max-width: 100%;
				text-align: center;
			}

			@include min768 {
				text-align: left;
			}

			@include min540 {
				max-width: rem(230);
			}
		}
	}

	&__fields {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		flex: 1;

		@include min1340 {
			justify-content: center;
			margin-left: auto;
			margin-right: auto;
		}

		@include min768 {
			flex-direction: column;
			width: 100%;
		}

		.field-set {
			flex-grow: 1;
			width: rem(266);

			margin-right: rem(30);

			@include min1600 {
				width: auto;
				margin-right: rem(14);
			}

			@include min1340 {
				width: calc(33.333% - 30px);
			}

			@include min768 {
				width: 100%;
				margin-right: 0;
				margin-bottom: rem(20);
			}
		}

		.field-checked {
			margin-right: rem(16);

			@include min1340 {
				width: calc(33.333% - 30px);
			}

			@include min768 {
				width: 100%;
				margin-right: 0;
			}
		}

		.button_green {
			height: rem(50);
			display: flex;
			align-items: center;

			@include min1340 {
				// flex: 0 0 100%;
				margin-top: rem(20);
			}

			@include min540 {
				width: 100%;
				justify-content: center;
			}
		}
	}
}

.field-set {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		top: rem(-6);
		right: rem(20);
		z-index: 1;
		background-color: var(--c-green-global);
		display: block;
		width: rem(12);
		height: rem(12);
		border-radius: 50%;
		border: rem(3) solid white;
	}
}

.field-set.field-not {
	&::before {
		display: none;
	}
}

.field {
	// position: relative;
	width: 100%;
	height: rem(50);
	border-radius: rem(30);
	background-color: #F2F2F2;
	padding: 0 rem(30);

	display: flex;
	align-items: center;

	font-size: rem(15);
	color: var(--c-gray);

	@include min1600 {
		font-size: rem(13);
		padding: 0 rem(20);
	}

	@include placeholder {
		color: var(--c-gray);
	}
}


textarea.field {
	padding: rem(18) rem(20);
	resize: vertical;

	min-height: rem(70);
	height: rem(70);
	max-height: rem(150);
}

.field-checked {
	display: flex;
	align-items: center;

	cursor: pointer;
	
	input {
		display: none;

		&:checked {
			~ .check {
				background-color: var(--c-green);

				svg {
					path {
						fill: white;
					}
				}
			}
		}
	}

	.check {
		width: rem(50);
		height: rem(50);
		flex: 0 0 rem(50);
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		background-color: var(--c-white-light);

		svg {
			path {
				fill: #4F4F4F;
			}
		}
	}

	.text {
		margin-left: rem(10);
		max-width: rem(226);

		color: var(--c-gray);
		font-size: rem(13);

		@include min1600 {
			font-size: rem(13);
		}
	}
}