.popup_modal {
	position: fixed;
	// top: 0;
	// left: 0;
	// width: 100%;
	// height: 100%;
	width: rem(642);
	overflow: hidden;
	height: auto;
	top: 25%;
	left: 50%;
	margin-left: rem(-321);
	background-color: white;

	border-radius: rem(20);

	z-index: 50;

	// display: flex;
	flex-direction: column;

	box-shadow: 0 0 10px rgba(0,0,0,.15);

	display: none;

	@include min768 {
		top: 0;
		left: 0;
		margin-left: 0;
		width: 100%;
		height: 100%;
		border-radius: 0;
		padding-top: rem(108);
	}

	&__close {
		position: absolute;
		top: rem(40);
		right: rem(40);
	}

	&__header {
		padding: rem(40) rem(40) rem(30) rem(40);

		position: relative;

		display: flex;
		flex-direction: column;

		.title {
			margin-bottom: rem(10);

			font-weight: bold;
			font-size: rem(24);
			color: var(--c-black);
		}

		.desc {
			color: var(--c-gray);
			font-size: rem(14);
		}
	}

	&__form {
		padding: 0 rem(40);

		margin-bottom: rem(30);

		display: flex;
		flex-direction: column;
		height: 100%;

		.field-set-group {
			display: flex;

			@include min540 {
				flex-wrap: wrap;
			}
			
			.field-set {
				flex: 0 0 calc((100% - 30px) / 2);
				max-width: calc((100% - 30px) / 2);
				margin-right: rem(30);

				@include min540 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-right: 0;
				}

				&:last-child {
					margin-right: 0;
				}
			}

			.field-button {
				flex: 0 0 calc((100% - 30px) / 2);
				max-width: calc((100% - 30px) / 2);
				margin-right: rem(30);

				@include min540 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-right: 0;
					order: 2;
				}

				.button_green {
					width: 100%;
					justify-content: center;
				}

				&:last-child {
					margin-right: 0;
				}
			}

			.field-checked {
				margin-bottom: 0;

				@include min540 {
					order: 1;
					margin-bottom: rem(25);
				}
			}
		}

		.field-set {
			margin-bottom: rem(30);
		}

		.field-checked {
			margin-bottom: rem(50);
		}

		.field-button {
			display: flex;
			justify-content: center;
			margin-top: auto;
		}
	}

	&__nav {
		margin-top: rem(32);
		padding: 0 rem(38);

		ul {
			display: flex;
			flex-direction: column;

			li {
				margin-bottom: rem(45);

				&:last-child {
					margin-bottom: 0;
				}

				a {
					font-size: rem(18);
					color: var(--c-bgray);
				}

				a.active {
					font-weight: bold;
				}
			}
		}
	}

	&__social {
		background-color: #F2F2F2;
		padding: rem(24) rem(30);
		margin-bottom: rem(58);

		p {
			color: var(--c-black);
			font-size: rem(14);
			text-align: center;
			margin-bottom: rem(17);
		}

		.socilal_links {
			display: flex;
			flex-wrap: wrap;
			justify-content: center;

			a {
				width: rem(40);
				height: rem(40);
				border-radius: 50%;
				margin-right: rem(30);

				background-color: white;

				display: flex;
				align-items: center;
				justify-content: center;

				svg {
					width: rem(18);
					height: rem(18);
					path {
						fill: var(--c-green);
					}
				}

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	&__contact {
		padding: 0 rem(30);
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		.block_text_right {
			flex-direction: row-reverse;
			margin-bottom: rem(40);

			&:last-child {
				margin-bottom: 0;
			}

			.text {
				align-items: flex-start;
				span {
					color: var(--c-gray);
					font-size: rem(14);
				}

				b {
					color: #312F30;
					font-size: rem(18);
				}
			}

			.image {
				margin-right: rem(22);
				svg {
					path {
						fill: var(--c-black);
					}
				}
			}
		}
	}

	&__bottom {
		margin-top: auto;
		margin-bottom: rem(30);

		padding: 0 rem(30);

		.button_green {
			width: 100%;
			height: rem(50);

			display: flex;
			justify-content: center;
			align-items: center;

			span {
				font-size: rem(13);
			}
		}
	}

	&__success {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		padding: rem(42) rem(30);

		.image {
			background-color: rgba(25,118,65,.1);
			width: rem(80);
			height: rem(80);
			border-radius: 50%;

			display: flex;
			align-items: center;
			justify-content: center;
			margin-bottom: rem(30);
		}

		.title {
			margin-bottom: rem(10);
			font-size: rem(24);
			font-weight: bold;
		}

		.desc {
			color: var(--c-gray);
			font-size: rem(14);
			text-align: center;
		}
	}
}

.popup_modal.active {
	@include min1200 {
		display: flex;
	}
	
}

.popup_modal_all {
	top: 15%;
	z-index: 52;
	padding-top: 0;

	@include min768 {
		top: 0;
	}
}

#modal_success {
	top: 25%;
	z-index: 52;
	padding-top: 0;

	@include min768 {
		top: 0;
	}
}

.popup_modal_all.active {
	display: flex;
}