.sect_colors {
	margin-bottom: rem(100);

	@include min540 {
		margin-bottom: 0;
	}
	
	> .container {
		display: flex;
		flex-wrap: wrap;
	}

	&__left {
		flex: 0 0 45%;
		max-width: 45%;


		@include min1340 {
			flex: 0 0 100%;
			max-width: 100%;
		}

		.title-box {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			margin-bottom: rem(80);

			@include min540 {
				margin-bottom: rem(20);
			}

			.slider-nav {
				@include max540 {
					display: none;
				}
			}
		}

		.title-36 {
			max-width: rem(622);
			

			@include min540 {
				max-width: rem(172);
				
			}
		}

		

		@include max540 {
			.swiper-wrapper {
				display: flex;
				flex-wrap: wrap;
				flex-direction: column;

				height: rem(600);

				.swiper-slide {
					width: calc((100% - 30px) / 2);
					height: rem(137);
					margin-bottom: rem(65);

					&:last-child,
					&:nth-child(3) {
						margin-bottom: 0;
					}
				}
			}
		}

		@include min768 {
			.swiper-wrapper {
				height: auto;
				margin-bottom: rem(32);

				.swiper-slide {
					width: 100%;
					margin-right: 0;
					height: auto;

					&:nth-child(3) {
						margin-bottom: rem(32);
					}
				}
			}
		}

		.item {
			display: flex;
			// flex-wrap: wrap;

			

			&__count {

				flex: 0 0 rem(28);
				max-width: rem(28);

				margin-right: rem(17);
				font-size: rem(48);
				font-weight: bold;
				// line-height: 180%;
				color: var(--c-white-light);
			}

			&__text {
				display: flex;
				flex-direction: column;

				padding-top: rem(10);

				b {
					font-size: rem(14);
					color: var(--c-bgray);
					margin-bottom: rem(14);
				}

				p {
					font-size: rem(15);
					line-height: rem(24);
					color: var(--c-gray);

					@include min540 {
						font-size: rem(14);
					}
				}
			}
		}
	}

	&__right {
		flex: 0 0 55%;
		max-width: 55%;

		padding-left: rem(36);

		@include min1340 {
			flex: 0 0 100%;
			max-width: 100%;
			padding-left: 0;

			> .flex {
				flex-wrap: wrap;
			}
		}

		.title {
			// margin-bottom: rem(56);
			margin-left: rem(110);

			display: flex;
			align-items: center;

			@include min1340 {
				margin-bottom: rem(32);
				margin-left: auto;
				margin-right: auto;
				text-align: center;
			}

			@include min540 {
				margin-bottom: rem(20);
				text-align: left;
			}

			svg {
				margin-top: rem(32);

				@include min1340 {
					display: none;
				}
			}

			.text {
				font-size: rem(24);
				font-weight: bold;
				font-family: 'Caveat', sans-serif;
				color: var(--c-gray);
				max-width: rem(290);
				margin-left: rem(20);

				@include min1340 {
					max-width: 100%;
					margin-left: 0;
					width: 100%;
				}

				> span {
					color: var(--c-green-global);
				}
			}
		}

		.colors {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			max-width: rem(140);

			@include min1340 {
				flex: 0 0 100%;
				max-width: 100%;
				justify-content: center;
			}

			@include min540 {
				justify-content: flex-start;
			}
		
			img {
				margin-bottom: rem(20);

				@include min1340 {
					margin-right: rem(20);
				}

				@include min540 {
					width: rem(30);
					height: rem(30);
					margin-right: rem(10);
					margin-bottom: rem(10);
				}
			}
		}

		.image {
			@include min1340 {
				margin-left: auto;
				margin-right: auto;
			}
		}
	}

}