.sect_contact {
	background-color: #F8F8F8;

	> .container {
		display: grid;
		grid-template-columns: calc((100% - var(--container))/2) 4fr 6fr calc((100% - var(--container))/2);
	
		@include min {
			display: flex;
			flex-direction: column;
			width: 100%;
			margin: 0;
		}
	}

	&__left {
		padding-top: rem(70);
		padding-bottom: rem(160);

		padding-right: rem(132);

		grid-column: 1 / span 2;

		margin-left: 20px;
		margin-right: 20px;

		@include min1050 {
			padding-right: rem(32);
		}

		@include min {
			flex: 0 0 100%;
			max-width: 100%;

			padding-top: rem(30);
			padding-right: 0;
			padding-bottom: rem(20);
		}

		.title-36 {
			margin-bottom: rem(46);

			@include min {
				margin-bottom: rem(20);
			}
		}

		p {
			margin-bottom: rem(30);
			font-size: rem(15);
			line-height: rem(24);
			color: var(--c-gray);
			max-width: rem(420);

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&__right {
		grid-column-end: span 2;

		@include min {
			flex: 0 0 100%;
			max-width: 100%;
		}

		img {
			position: relative;
			top: rem(-32);
			border-top-left-radius: rem(30);
			border-top-right-radius: rem(30);
			width: 100%;
			height: 100%;
			object-fit: cover;

			@include min {
				border-radius: 0;
				top: 0;
			}

			@include min540 {
				height: rem(440);
			}
		}
	}
}