.sect_advantage {
	margin-bottom: rem(100);

	@include min768 {
		margin-bottom: rem(30);
	}

	.title-box {
		position: relative;
		margin-bottom: rem(70);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		@include min540 {
			margin-bottom: rem(32);
		}

		.left {
			.title-36 {
				margin-bottom: rem(3);

				@include min540 {
					max-width: rem(129);
				}
			}

			p {
				font-size: rem(18);
				color: #888888;

				@include min540 {
					font-size: rem(14);
				}
			}
		}

		.right {
			@include max540 {
				display: none;
			}
			@include min540 {
				position: absolute;
				top: 0;
				right: 0;
			}
		}
	}

	.swiper-container {

		@include max540 {
			display: flex;
			flex-wrap: wrap;
		}

		.swiper-wrapper {
			@include max540 {
				display: flex;
				flex-wrap: wrap;
			}
		}
		
		.swiper-slide {
			@include max540 {
				width: calc((100% - 90px) / 4);
				margin-right: rem(30);
				margin-bottom: rem(50);

				@include min1050 {
					width: calc((100% - 30px) / 2);
					margin-right: rem(30);
					&:nth-child(2n) {
						margin-right: 0;
					}

					
				}

				&:nth-child(4n) {
					margin-right: 0;
				}
			}
		}
		

		.item {
			// width: calc((100% - 90px) / 4);
			// margin-right: rem(30);
			// margin-bottom: rem(50);

			display: flex;

			&:nth-child(4n) {
				margin-right: 0;
			}

			&__left {
				flex: 0 0 rem(60);
				width: rem(60);
				height: rem(60);
				margin-right: rem(30);

				@include min540 {
					flex: 0 0 rem(40);
					width: rem(40);
					height: rem(40);

					margin-right: rem(20);
				}
			}

			&__right {
				display: flex;
				flex-direction: column;

				b {
					margin-bottom: rem(14);
					font-size: rem(18);
					line-height: rem(24);
					color: var(--c-bgray);

					@include min540 {
						font-size: rem(14);
						line-height: auto;
						margin-bottom: rem(10);
					}
				}

				p {
					font-size: rem(15);
					line-height: rem(24);
					color: var(--c-gray);

					@include min540 {
						font-size: rem(14);
					}
				}
			}
		}
	}
}