.sect_product {
	margin-bottom: rem(120);

	@include min768 {
		margin-bottom: rem(20);
	}

	.title-36 {
		margin-bottom: rem(60);

		@include min768 {
			margin-bottom: rem(20);
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
		min-width: 0;
		position: relative;

		&-nav {
			position: absolute;
			top: 50%;
			width: 100%;

			@include min768 {
				top: 135px;
			}

		}

		&-all {
			margin-top: rem(60);
			width: 100%;

			@include min768 {
				display: none;
			}

			a {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				height: rem(60);
				border-radius: rem(60);
				background-color: var(--c-green-global);

				span {
					margin-right: rem(10);
					font-size: 14px;
					font-weight: 700;
					letter-spacing: 5%;
					text-transform: lowercase;
					color: white;
				}
			}
		}

		.swiper-container {
			padding: 30px;
			margin: -30px;

			@include min540 {
				padding: 0;
				margin: 0;
			}

			.swiper-slide, .swiper-slide-duplicate-active {
				opacity: 0;
				transition: 250ms ease;
			}
			.swiper-slide-active {
				opacity: 1;

				~ .swiper-slide {
					opacity: 1;
				}
				~ .swiper-slide-duplicate-active {
					opacity: 0;
				}
			}
		}

		.slider-left, .slider-right {
			position: absolute;
			top: 50%;
			transform: translateY(-80px);
			z-index: 10;
			width: rem(80);
			height: rem(80);
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: white;
			border-radius: 50%;
			box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.1);

			@include min1600 {
				width: rem(60);
				height: rem(60);
				transform: translateY(-150px);
			}

			@include min768 {
				top: 0;
				width: rem(60);
				height: rem(60);
				transform: translateY(0px);

				svg {
					width: rem(20);
					height: rem(20);
				}
			}
		}

		.slider-left {
			left: rem(-10);

			@include max {
				left: rem(-90);
			}
		}

		.slider-right {
			right: rem(-10);

			@include max {
				right: rem(-90);
			}
		}

		// .item_product {
		// 	flex: 0 0 calc((100% - 90px) / 4);
		// 	max-width: calc((100% - 90px) / 4);
		// 	margin-right: 30px;

		// 	&:last-child {
		// 		margin-right: 0;
		// 	}
		// }
	}
}

.item_product {
	position: relative;
	display: flex;
	flex-direction: column;
	padding: rem(30);
	background-color: white;
	box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05), 0px 30px 20px rgba(0, 0, 0, 0.03);
	border-radius: 30px;

	@include min1600 {
		padding: rem(20);
	}

	@include min540 {
		border-radius: rem(20);
		box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.05), 0px 30px 20px rgba(0, 0, 0, 0.03);
		
		margin-left: -20px;
		width: calc(100% + 40px);
	}

	&__abs {
		position: absolute;
		top: rem(30);
		left: rem(30);
		z-index: 2;

		display: flex;
		flex-direction: column;
		align-items: baseline;

		@include min1600 {
			top: rem(20);
			left: rem(20);
		}

		// @include min540 {
		// 	top: 0;
		// 	left: 0;
		// }
		
		span {
			height: rem(24);
			display: flex;
			align-items: center;
			border-radius: rem(20);
			padding: 0 rem(12);
			color: white;
			font-size: rem(12);
			font-weight: 500;
			margin-bottom: rem(10);

			&:last-child {
				margin-bottom: 0;
			}
		}

		&-green {
			background-color: #71C476;
		}
		&-orange {
			background-color: #FE9838;
		}
	}

	&__image {
		margin-bottom: rem(30);
		display: flex;
		align-items: center;
		justify-content: center;
		height: 250px;

		img {
			max-width: rem(165);
		}
	}

	&__title {
		font-size: rem(18);
		font-weight: 500;
		line-height: rem(22);
		color: black;
		margin-bottom: rem(20);
	}

	&__list {
		display: flex;
		flex-direction: column;

		margin-bottom: rem(20);

		li {
			position: relative;
			font-size: rem(12);
			font-weight: 600;
			line-height: rem(20);
			color: var(--c-gray-light);
			display: flex;
			align-items: center;
			padding-left: rem(10);

			&::before {
				content: "";
				position: absolute;
				top: 50%;
				left: 0;
				transform: translateY(-4px);
				width: 4px;
				height: 4px;
				border-radius: 50%;
				background-color: var(--c-green-global);
			}

			span {
				color: #7B818C;
				margin-left: rem(3);
			}
		}
	}

	&__checked {
		margin-bottom: rem(20);

		.field-checked {
			margin-bottom: rem(9);

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	&__price {
		margin-bottom: rem(25);
		display: flex;
		align-items: center;

		@include min540 {
			flex-direction: column;
			align-items: baseline;
		}

		b {
			margin-right: rem(20);
			font-weight: bold;
			font-size: rem(24);

			@include min540 {
				margin-bottom: rem(4);
				margin-right: 0;
			}
		}

		span {
			font-size: rem(14);
			font-weight: bold;
			color: #DD8888;
			text-decoration: line-through;
		}
	}

	&__control {
		display: flex;
		align-items: center;

		.number-counter {
			margin-right: rem(30);

			@include min540 {
				margin-right: 0;
				position: absolute;
				bottom: 95px;
    			right: 34px;
			}
		}

		.button {
			display: flex;
			align-items: center;
			height: rem(50);
			background-color: var(--c-green-global);
			border-radius: rem(50);
			padding: 0 rem(40);
			transition: 250ms ease;

			&:hover {
				background-color: var(--c-green);
			}

			@include min1600 {
				padding: 0 rem(28);
			}

			@include min540 {
				width: 100%;
			}

			svg {
				margin-right: rem(5);
			}

			span {
				font-size: rem(14);
				font-weight: bold;
				color: white;
				white-space: nowrap;
			}
		}
	}
}