.page_product {
	margin-bottom: rem(60);
	margin-top: rem(90);
	// position: relative;

	@include min1050 {
		margin-top: rem(50);
		margin-bottom: rem(30);
	}

	@include min768 {
		margin-bottom: 0;
	}

	.container {
		position: relative;
	}

	&__top {
		position: absolute;
		top: 0;
		right: 0;
		display: flex;
		flex-direction: column;

		flex: 0 0 calc((100% - 30px) / 2);
		max-width: calc((100% - 30px) / 2);

		@include min1340 {
			position: relative;
			flex: 0 0 100%;
			max-width: 100%;
			right: auto;
			left: 0;
			margin-bottom: rem(20);
		}

		.title-h1 {
			font-size: rem(36);
			color: var(--c-black);
			font-weight: 800;

			@include min768 {
				font-size: rem(24);
			}
		}
	}

	&__row {
		display: flex;
		flex-wrap: wrap;

		&-left, &-right {
			flex: 0 0 calc((100% - 30px) / 2);
			max-width: calc((100% - 30px) / 2);
			margin-right: rem(30);

			@include min1340 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-right: 0;
			}

			&:last-child {
				margin-right: 0;
			}
		}

		&-left {
			background-color: #F8F8F8;
			border-radius: rem(30);
			padding: rem(54) rem(78) rem(54) rem(70);

			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			@include min1600 {
				padding: rem(24) rem(48) rem(24) rem(40);
			}

			@include min1340 {
				margin-bottom: rem(30);
			}

			@include min768 {
				padding: rem(20);
				margin-bottom: rem(20);
			}

			.info {
				margin-top: rem(20);
				display: flex;
				flex-direction: column;

				@include min768 {
					margin-top: 0;
					flex: 0 0 100%;
					max-width: 100%;
				}

				&__top {
					display: flex;
					flex-wrap: wrap;
					align-items: center;
					margin-bottom: rem(40);

					@include min768 {
						margin-bottom: rem(20);
					}

					span {
						margin-right: rem(10);
						height: rem(24);
						font-size: rem(12);
						display: flex;
						align-items: center;
						border-radius: rem(20);
						color: white;
						padding: 0 rem(12);

						&:last-child {
							margin-right: 0;
						}
					}

					.block-green {
						background-color: #71C476;
					}
					.block-orange {
						background-color: #FE9838;
					}
				}

				&__title {
					margin-bottom: rem(90);
					font-size: rem(18);
					font-weight: 800;
					color: #4F4F4F;
					text-transform: uppercase;

					@include min768 {
						margin-bottom: rem(20);
						font-weight: 900;
					}
				}

				&__list {
					display: flex;
					flex-direction: column;

					&-item {
						display: flex;
						align-items: center;
						margin-bottom: rem(50);

						@include min768 {
							margin-bottom: rem(20);
						}

						&:last-child {
							margin-bottom: 0;
						}

						&__image {
							width: rem(40);
							height: rem(40);
							display: flex;
							align-items: center;
							justify-content: center;
							margin-right: rem(20);

							@include min768 {
								margin-right: rem(15);
							}
						}

						&__text {
							display: flex;
							flex-direction: column;

							b {
								margin-bottom: rem(6);
								font-size: rem(12);
								font-weight: bold;
								color: #4F4F4F;
								text-transform: uppercase;
							}

							span {
								font-weight: 300;
								font-size: rem(18);
								color: #4F4F4F;
							}
						}
					}
				}
			}

			.image {
				flex: 0 0 rem(300);
				max-width: rem(300);

				@include min1600 {
					flex: 0 0 rem(240);
					max-width: rem(240);
					margin-top: rem(50);
				}

				@include min768 {
					flex: 0 0 100%;
					max-width: 100%;
					margin-top: rem(20);
					margin-bottom: rem(20);
					display: flex;
					justify-content: center;
				}

				@include min540 {
					img {
						max-width: rem(185);
					}
				}
			}
		}

		&-right {
			margin-top: rem(150);

			@include min1340 {
				margin-top: 0;
			}

			.info_list {
				display: flex;
				align-items: center;

				@include min768 {
					justify-content: space-between;
				}

				&__item {
					margin-right: rem(45);
					margin-bottom: rem(40);

					@include min768 {
						margin-right: 0;
						margin-bottom: rem(20);
					}

					span {
						font-size: rem(14);
					}

					&:first-child {
						span {
							color: var(--c-green-global);
						}
					}

					&:last-child {
						span {
							color: #7C7C7C;

							b {
								color: #2F2F2F;
								font-weight: normal;
							}
						}
					}
				}
			}

			.price_control {
				background-color: #F8F8F8;
				border-radius: rem(30);
				padding: rem(40);
				display: flex;
				align-items: center;
				justify-content: space-between;

				margin-bottom: rem(25);

				@include min1600 {
					padding: rem(20);
				}

				@include min768 {
					border-radius: 0;
					margin-bottom: rem(20);
					margin-left: -20px;
					width: calc(100% + 40px);
				}

				@include min540 {
					flex-wrap: wrap;
				}

				&__left {
					display: flex;
					align-items: center;
					
					@include min1340 {
						flex-direction: column;
						align-items: baseline;
					}

					b {
						margin-right: rem(20);
						font-size: rem(24);
						font-weight: bold;
						color: var(--c-black);

						@include min1340 {
							margin-right: 0;
							margin-bottom: rem(5);
						}
					}

					span {
						color: #DD8888;
						font-size: rem(14);
						font-weight: bold;
						text-decoration: line-through;
					}
				}

				&__right {
					display: flex;
					align-items: center;
					margin-left: auto;
					margin-right: rem(30);

					.number-counter {
						// margin-right: rem(30);
						max-width: rem(106);
					}

					
				}

				.button {
					display: flex;
					align-items: center;
					height: rem(50);
					background-color: var(--c-green-global);
					border-radius: rem(50);
					padding: 0 rem(40);

					@include min540 {
						width: 100%;
						flex: 0 0 100%;
						max-width: 100%;
						margin-top: rem(30);
						justify-content: center;
					}

					svg {
						margin-right: rem(5);
					}
					span {
						font-size: rem(14);
						font-weight: bold;
						color: white;
						white-space: nowrap;
					}
				}
			}

			.option_list {
				display: flex;
				flex-direction: column;
				padding: 0 rem(30);

				@include min1600 {
					padding: 0;
				}

				&__title {
					margin-bottom: rem(30);
					font-size: rem(16);
					font-weight: 500;

					@include min540 {
						margin-bottom: rem(20);
					}
				}

				&__item {
					display: flex;
					align-items: center;
					justify-content: space-between;
					margin-bottom: rem(15);

					@include min540 {
						flex-direction: column;
						justify-content: baseline;
						align-items: baseline;

						.field-checked {

						}
					}

					&:last-child {
						margin-bottom: 0;
					}

					&-right {
						display: flex;
						align-items: center;

						@include min540 {
							margin-left: rem(26);
						}

						b {
							font-size: rem(12);
							font-weight: normal;
							width: rem(80);
							margin-right: rem(4);
							text-align: left;

							@include min540 {
								width: rem(62);
							}
						}

						span {
							width: rem(70);
							
							white-space: nowrap;
							font-size: rem(10);
							color: #DD8888;
							text-decoration: line-through;
						}
					}
				}
			}
		}
	}

	&__desc {
		margin-top: rem(32);
		margin-bottom: rem(40);

		.title-36 {
			margin-bottom: rem(10);
		}

		p {
			font-size: rem(14);
			line-height: rem(24);
			color: #7B818C;

			margin-bottom: rem(24);

			@include min540 {
				margin-bottom: rem(10);
			}

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	&__images {
		margin-bottom: rem(60);

		display: flex;
		flex-wrap: wrap;

		@include min768 {
			margin-bottom: 0;
		}

		&-item {
			flex: 0 0 calc((100% - 90px) / 4);
			max-width: calc((100% - 90px) / 4);
			margin-right: rem(30);

			border-radius: rem(30);
			overflow: hidden;

			@include min768 {
				flex: 0 0 calc((100% - 20px) / 2);
				max-width: calc((100% - 20px) / 2);
				margin-right: rem(20);
				margin-bottom: rem(20);

				&:nth-child(2n) {
					margin-right: 0;
					
				}
			}

			@include min540 {
				flex: 0 0 100%;
				max-width: 100%;
				margin-right: 0;
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
			@include max768 {
				&:nth-child(4n) {
					margin-right: 0;
				}
			}
			
		}
	}
}