:root {
	--font-default: 'Montserrat', sans-serif;
	--container-maxw: 1760px;
	--container: 1600px;

	--mobile-px: 20px;

	--c-black: #333333;
	--c-gray: #828282;
	--c-bgray: #4F4F4F;
	--c-gray-light: #BDBDBD;
	--c-green-global: #219653;
	--c-green: #197641;
	--c-white-light: #F2F2F2;

	--c-yellow: #EEC900;
	--c-yellow-light: #F2C94C;

	--transition: 250ms ease;
}

* {
	outline: none;
	border: 0;
	margin: 0;
	padding: 0;
}

html, body {
	// position: relative;
	
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// overflow-x: hidden;
	// height: 100%;

	@include min1600 {
		// font-size: 87%;
	}
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

a, button {
	transition: var(--transition);
}

li {
	list-style: none;
}

body {
	// position: relative;
	font-family: var(--font-default);
	font-size: rem(16);
	// height: 100%;
	min-width: 320px;
	
	// -webkit-overflow-scrolling: touch;
}

.relative {
	position: relative;
}

.container {
	max-width: var(--container);
	margin: 0 auto;

	@include min1600 {
		margin: 0 20px;
	}
}

.container-max {
	max-width: var(--container-maxw);
	margin: 0 auto;

	@include min1600 {
		margin: 0 20px;
	}
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.w-main {
	overflow-x: hidden;
}