.sect_reviews {
	margin-bottom: rem(120);

	@include min768 {
		margin-bottom: rem(30);
	}

	>.container {
		display: flex;
		// display: grid;
		// grid-template-columns: calc((100% - var(--container))/2) 5fr 5fr calc((100% - var(--container))/2);
		// flex-wrap: wrap;

		@include min1340 {
			flex-wrap: wrap;
		}
	}

	&__left {
		display: flex;
		flex-direction: column;

		flex: 0 0 21.88%;
		max-width: 21.88%;

		margin-right: rem(240);

		@include min1600 {
			flex: 0 0 30%;
			max-width: 30%;
			margin-right: rem(180);
		}

		@include min1340 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-right: 0;

			margin-bottom: rem(32);
		}

		@include min540 {
			margin-bottom: rem(20);
		}

		// margin-right: rem(240);
		// grid-column: 1 / span 2;

		.title-36 {
			margin-bottom: rem(26);

			@include min540 {
				max-width: rem(176);
				margin-bottom: rem(10);
			}
		}

		.subtitle {
			font-size: rem(18);
			line-height: 140%;
			margin-bottom: rem(30);

			color: var(--c-black);

			@include min540 {
				font-size: rem(14);
				margin-bottom: 0;
				display: block;
			}
		}

		p {
			font-size: rem(15);
			line-height: rem(24);
			color: var(--c-gray);
			margin-bottom: rem(30);

			&:last-of-type {
				margin-bottom: 0;
			}

			@include min540 {
				display: none;
			}
		}

		.link {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			margin-top: rem(24);

			@include min540 {
				display: none;
			}

			&:hover {
				span {
					color: var(--c-yellow);
				}
			}

			img {
				margin-right: rem(20);
			}

			span {
				transition: var(--transition);
				font-size: rem(14);
				font-weight: bold;
				color: var(--c-black);
				text-transform: uppercase;
				max-width: rem(184);
			}
		}
	}

	&__smart {

		display: flex;
		flex-direction: column;
		p {
			font-size: rem(15);
			line-height: rem(24);
			color: var(--c-gray);
			margin-bottom: rem(30);

			&:last-of-type {
				margin-bottom: 0;
			}
		}

		.link {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			margin-top: rem(24);

			&:hover {
				span {
					color: var(--c-yellow);
				}
			}

			img {
				margin-right: rem(20);
			}

			span {
				transition: var(--transition);
				font-size: rem(14);
				font-weight: bold;
				color: var(--c-black);
				text-transform: uppercase;
				max-width: rem(184);
			}
		}

		@include max540 {
			display: none;
		}
	}

	&__right {
		position: relative;
		align-self: flex-start;
		flex: 0 0 70%;
		max-width: 70%;

		@include min1600 {
			flex: 0 0 58%;
			max-width: 58%;
			// margin-top: rem(96);
		}

		@include min1340 {
			flex: 0 0 100%;
			max-width: 100%;
			margin-top: 0;
		}

		.slider-nav {
			position: absolute;
			top: 50%;
			margin-top: rem(-20);
			left: 0;
			z-index: 10;
			width: 375px !important;
			height: 0;

			@include min1600 {
				left: 0;
				width: 282px !important;
			}

			@include min1440 {
				width: 253px !important;
			}

			@include min1340 {
				width: 100% !important;
				left: 0;

				
			}

			.slider-prev,
				.slider-next {
					position: absolute;
					top: 0;
					width: rem(60);
					height: rem(60);
					svg {
						width: rem(12) !important;
						height: rem(20) !important;
					}
				}

			.slider-prev {	
				left: rem(-30);
			}

			.slider-next {
				
				right: rem(-30);
			}
		}

		// .swiper-container {
		// 	margin: 0 -80px;
		// 	padding: 0 80px;
		// }

		.swiper-wrapper {
			align-items: center;
		}

		.swiper-slide {
			// margin-right: rem(50);

			// width: rem(385);
			// height: rem(684);
			width: rem(324);
			height: rem(574);

			// transition: var(--transition);

			display: flex;
			justify-content: center;
			align-items: center;

			@include min1600 {
				width: rem(280);
				height: rem(524);
				// margin-right: rem(20);
			}

			.reviews__item {
				position: relative;
				width: 100%;
				// width: calc(100% - 40px);
				// width: rem(324);
				// height: rem(574);

				// width: rem(280);
				height: rem(574);

				@include min1600 {
					width: calc(100% - 20px);
					height: rem(424);
				}

				@include min1340 {
					width: 100%;
					height: rem(497);
				}

				&::before {
					content: "";
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-color: rgba(33, 150, 83, .7);
				}

				
			}

			&:last-child {
				
			}
		}

		.swiper-slide:not(.swiper-slide-active) {
			// margin: 50px 50px 50px 0;

			.reviews__item {
				margin: 25px;

				@include min1200 {
					margin: 0;
				}
			}
		}
		// .swiper-slide-prev {
		// 	margin-right: 100px !important;
		// }
		// .swiper-slide-next {
		// 	// margin-left: 50px !important;
		// 	.reviews__item {
		// 		margin-left: 50px !important;
		// 	}
		// }

		.swiper-slide-active {
			// width: rem(385);
			height: rem(684);
			// transition: var(--transition);

			.reviews__item {
				// width: rem(385);
				width: 100%;
				height: rem(684);
				&::before {
					opacity: 0;
				}
					
				@include min1600 {
					// width: rem(335);
					height: rem(480);
				}

				@include min1340 {
					height: rem(497);
				}
			}
		}

		.reviews__item {
			border-radius: rem(30);
			height: 100%;
			overflow: hidden;

			transition: var(--transition);

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}